<template>
  <Fieldset class="mb-6 rounded-xl">
    <template #label>Buyback Number</template>
    <template #default>
      <form
        autocomplete="off"
        @submit.prevent="handleSave">
        <div class="grid grid-cols-1 gap-4">
          <div>
            <Input
              v-maska="'S*'"
              v-model="form.prefix"
              v-loading="loading.all"
              :disabled="loading.update"
              :view="(validation.prefix.$invalid && 'danger') || 'default'"
              autocomplete="off"
              label="Buyback Prefix"
              class="w-full"
            />
            <small
              class="text-red-base block"
              v-for="(error, errorIndex) of validation.prefix.$errors"
              :key="errorIndex">
              {{ error.$message }}.
            </small>
          </div>
          <div>
            <Input
              v-model="preview"
              v-loading="loading.all || loading.update"
              label="Preview"
              class="w-full"
              disabled
            />
          </div>
          <div class="flex justify-end">
            <Button
              color="teal"
              dense
              type="submit"
              @click="handleSave">
              Save & Apply
            </Button>
          </div>
        </div>
      </form>
    </template>
  </Fieldset>
</template>
<script>
import { computed, defineComponent, reactive, watch } from 'vue'

import { useVuelidate } from '@vuelidate/core'
import { required } from '@vuelidate/validators'

import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Button from 'bbUi/components/button/button.vue'
import Input from 'bbUi/components/input/input.vue'

export default defineComponent({
  name: 'WidgetPrefix',
  components: {
    Fieldset,
    Button,
    Input
  },
  props: {
    modelValue: {
      type: Object,
      default: undefined
    },
    loading: {
      type: Object,
      default: () => ({})
    }
  },
  emits: ['update:modelValue'],
  setup(props) {
    const data = computed({
      get: () => props.modelValue,
      set: (value) => emit('update:modelVallue', value)
    })
    const preview = computed(() =>
      (form.prefix && `${form.prefix}0X8R1`) || '0X8R1'
    )
    const form = reactive({ prefix: data.value.prefix_order_number_sequence })
    const rules = {
      prefix: { required }
    }
    const validation = useVuelidate(rules, form, { $lazy: true })

    const handleSave = async () => {
      await validation.value.$validate()
      if (!validation.value.$invalid) {
        data.value.prefix_order_number_sequence = form.prefix
      }
    }

    watch(
      () => data.value.prefix_order_number_sequence,
      (val) => {
        if (val) form.prefix = val
      },
      { immediate: true, deep: true }
    )

    return {
      form,
      preview,
      validation,
      handleSave
    }
  }
})
</script>
