<template>
  <div class="space-y-6 mb-2">
    <div class="flex items-center gap-x-1">
      <Checkbox
        v-model="state.isSwitch"
        :disabled="disableSwitch"
        type="toggle"
        color="success"
        @update:modelValue="handleSwitch"
      />
      <label class="font-semibold">
        Specific Location
      </label>
    </div>
    <Select
      v-model="selectedLocation"
      :loading="loading"
      :items="locations"
      :disabled="!state.isSwitch"
      placeholder="Choose Location"
      max-height="280px"
      name="name"
      by="token_location"
      no-border
      block>
      <template #prepend>
        <div class="px-4 py-4 w-full">
          <Input
            v-model="query.search"
            placeholder="Search Location"
            class="w-full"
            block
            icon>
            <template #icon>
              <span class="material-icons">search</span>
            </template>
          </Input>
        </div>
      </template>
      <template #item="{ item }">
        <div
          :class="[
            selectedLocation && (item.token_location === selectedLocation) ? 'text-teal-04 font-semibold' : '',
            'flex gap-x-2 items-center'
          ]">
          {{ item.name }}
          <span
            v-if="selectedLocation && (item.token_location === selectedLocation)"
            class="material-icons text-lg text-teal-04">
            check_circle
          </span>
        </div>
      </template>
      <template #append>
        <div class="py-2 transform scale-75">
          <Pagination
            v-model:page="query.page"
            v-model:limit="query.limit"
            :data="state.locations"
            hide-show-entries
            hide-detail-page
            small
          />
        </div>
      </template>
    </Select>
  </div>
</template>

<script>
import { defineComponent, watch, computed } from 'vue'
import debounce from 'utils/debounce'
import { useWidgetLandingByLocation } from 'composable/setting'

import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Input from 'bbUi/components/input/input.vue'
import Pagination from 'components/Pagination.vue'
import Select from 'components/Select.vue'

export default defineComponent({
  name: 'WidgetLandingLocations',
  components: {
    Pagination,
    Checkbox,
    Select,
    Input
  },
  props: {
    modelValue: {
      type: [Array, Object, String, Number, Boolean],
      default: undefined
    },
    selectedProduct: {
      type: Object,
      default: () => ({})
    },
    isReset: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    // Current Location on Top Navigation
    const { loading, state, query, show, currentLocation } = useWidgetLandingByLocation()
    const product = computed(() => props.selectedProduct)
    const locations = computed(() => {
      return (
        state.value.locations &&
        state.value.locations.data &&
        state.value.locations.data
      ) || (
        state.value.locations &&
        Array.isArray(state.value.locations) &&
        state.value.locations
      ) || []
    })
    const selectedLocation = computed({
      get: () => props.modelValue,
      set: (value) => emit('update:modelValue', value)
    })
    const disableSwitch = computed(() => {
      // const results = []
      // Object.keys(props.selectedProduct).forEach(item => 
      //   props.selectedProduct.hasOwnProperty(item) && props.selectedProduct[item] && results.push(item)
      // )
      return false // !results.length
    })

    const handleActionShow = async () => {
      await show((product.value && { ...props.selectedProduct }) || undefined)
    }
    const handleAutoSelect = () => {
      const location = !!locations.value.length && [...locations.value].find(i => {
        // i.id === currentLocation.value.id
        return i.name === currentLocation.value.name
      }) || undefined

      if (location) selectedLocation.value = location.token_location
    }
    const handleSwitch = async () => {
      state.value.locations = {}
      selectedLocation.value = null
      if (state.value.isSwitch) {
        debounce(async () => {
          await handleActionShow()
          handleAutoSelect()
        }, 300)();
      }
    }
    const handleReset = () => {
      selectedLocation.value = null
      state.value.isSwitch = false
      state.value.locations = []
    }
    watch(
      () => ({
       search: query.value.search,
       page: query.value.page
      }),
      () => {
        if (!state.value.isSwitch) return

        debounce(async () => {
          await handleActionShow()
        }, 500)();
      }
    )

    watch(() => props.selectedProduct.search, (value) => {
      handleReset()
    })

    return {
      handleSwitch,
      currentLocation,
      selectedLocation,
      disableSwitch,
      locations,
      product,
      loading,
      state,
      query,
      show
    }
  }
})
</script>
