<script>
import { computed, defineComponent, reactive, ref, watch } from 'vue'
import { useVuelidate } from '@vuelidate/core'
import { required } from "@vuelidate/validators"
import { usePaywall } from 'composable/paywall'
import { useUser } from 'composable/user'
import { useRouter } from 'vue-router'
import debounce from 'utils/debounce'
import handleCopy from 'utils/copy'

import WidgetCode from 'components/Widget/Controller/Code/index.vue'
import WidgetTermsConditions from 'components/Widget/Controller/TermsConditions/index.vue'
import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import FloatingSheets from 'components/FloatingSheets.vue'
import Button from 'bbUi/components/button/button.vue'
import ColorPicker from 'components/ColorPicker/index.vue'
import PopoverDark from 'components/PopoverDark.vue'
import Input from 'bbUi/components/input/input.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Radio from 'bbUi/components/radio/radio.vue'
import SelectLanguage from 'components/Select.vue'
import Alert from 'bbUi/components/alert/alert.vue'

export default defineComponent({
  name: 'WidgetSetting',
  components: {
    Alert,
    Modal,
    Input,
    Radio,
    Button,
    Fieldset,
    Checkbox,
    WidgetCode,
    ColorPicker,
    PopoverDark,
    SelectLanguage,
    FloatingSheets,
    WidgetTermsConditions
  },
  props: {
    modelValue: {
      type: Object,
      default: undefined
    },
    loading: {
      type: Object,
      default: undefined
    },
    proplan: {
      type: Boolean,
      default: false
    },
    brand: {
      type: Boolean,
      default: false
    },
    brandFree: {
      type: Boolean,
      default: false
    },
    whiteLabel: {
      type: Boolean,
      default: false
    },
    isPaywalledWidgetWatermark: {
      type: Boolean,
      default: false
    },
    code: {
      type: String,
      default: undefined
    },
    landing: {
      type: Object,
      default: undefined
    }
  },
  emits: ['update:modelValue', 'update:removeBrand', 'update:language', 'update:secondaryLanguage'],
  setup(props, { emit }) {
    const router = useRouter()
    const { open: openPaywall } = usePaywall()
    const  { me } = useUser()
    const data = computed({
      get: () => props.modelValue,
      set: (value) => emit('update:modelValue', value)
    })
    const modal = ref(false)
    const language = ref({
      name: me.value.lang_name,
      code: me.value.lang_code,
      label: `${me.value.lang_name} (${me.value.lang_code.toUpperCase()})`,
      search: ''
    })
    const languageSecondary = ref({
      name: null,
      code: null,
      label: null,
      search: ''
    })
    const languages = computed(() => {
      if (!data.value.languages.length) return []
      if (language.value.search === '') return data.value.languages.map(item => ({
        ...item,
        label: `${item.name} (${item.code.toUpperCase()})`
      }))

      const filtered = [...data.value.languages].filter((item) =>
        item.name.toLowerCase().includes(language.value.search.toLowerCase())
      )

      return filtered.map(item => ({
        ...item,
        label: `${item.name} (${item.code.toUpperCase()})`
      }))
    })
    const languagesSecondary = computed(() => {
      if (!data.value.languages.length) return []
      if (languageSecondary.value.search === '') return data.value.languages.map(item => ({
        ...item,
        label: `${item.name} (${item.code.toUpperCase()})`
      }))

      const filtered = [...data.value.languages].filter((item) =>
        item.name.toLowerCase().includes(languageSecondary.value.search.toLowerCase())
      )

      return filtered.map(item => ({
        ...item,
        label: `${item.name} (${item.code.toUpperCase()})`
      }))
    })
    const showBuybackBranding = computed({
      get: () => (data.value.widget_is_remove_brand == 1 ? 0 : 1),
      set: async (value) => {
        data.value.widget_is_remove_brand = value == 1 ? 0 : 1
      }
    })
    const isRealPorductIcon = computed(() => {
      const listOfAllowedPackageId = [4, 12, 13, 15, 16, 17, 18]
      return (listOfAllowedPackageId.includes(me.value.package_id)) ? false : true
    })
    const showCode = computed(() => !!languageSecondary.value.label)
    const computedCode = computed(() => {
      let scriptUniqueCode = props.code
      try {
        let lines = scriptUniqueCode.split('\n')
        let findkey = undefined
        lines.forEach((item, key) => {
          if (item.includes('disableFloatButton')) {
            findkey = {
              value: item,
              key
            }
            return
          }
        })
        const replacer = (slug, value) => {
          return lines.splice(
            findkey.key,
            0,
            findkey.value.replace(
              'disableFloatButton: false',
              `${slug}: "${value}",`
            ))
        }

        if (findkey && props.landing) {
          if (props.landing.device_slug)
            replacer('deviceSlug', props.landing.device_slug)
          if (props.landing.brand_slug)
            replacer('brandSlug', props.landing.brand_slug)
          if (props.landing.category_slug)
            replacer('categorySlug', props.landing.category_slug)
          if (props.landing.token_model)
            replacer('tokenModel', props.landing.token_model)
        }

        if (languageSecondary.value.code) {
          const lastParamIdx = lines.findIndex(line => line.includes('disableFloatButton'))
          lines.splice(lastParamIdx, 0 , `            language: "${languageSecondary.value.code}",`)
        }

        scriptUniqueCode = lines.join(`\n`)
      } catch (err) {
        console.error(`Error generating widget code: ${err}`)
      }

      return scriptUniqueCode
    })
    const dataTrendGraph = reactive({
      enable: data.value.widget_is_enable_price_trend_graph,
      title: data.value.widget_price_trend_graph_title,
      current: data.value.widget_price_trend_graph_current,
      expected: data.value.widget_price_trend_graph_next_week
    })
    const validationGraph = useVuelidate({
      title: { required },
      current: { required },
      expected: { required }
    }, dataTrendGraph, {
      $lazy: true
    })

    const handlePrimaryColor = debounce((color) => {
      data.value.widget_color_selected = color
    }, 500)
    const handleBackgroundColor = debounce((color) => {
      data.value.widget_background_color = color
    }, 500)
    const handleSelectButton = debounce((color, callback) => {
      data.value.widget_color_secondary_selected = color
      callback()
    }, 500)
    const handleOpenBilling = async () => {
      router.push({
        name: 'settingBilling'
      })
      // const response = await store.dispatch('billing/stripePortal')
      // if (response.url) window.open(response?.url)
    }
    const handleRemoveBranding = () => {
      modal.value = true
      showBuybackBranding.value = 0
      emit('update:removeBrand')
    }
    const handleChangeLanguage = (e) => {
      language.value.code = e.code
      language.value.name = e.name
      language.value.label = e.label
      language.value.search = ''
      emit('update:language', language.value)
    }
    const handleChangeLanguageSecondary = (e) => {
      languageSecondary.value.code = e.code
      languageSecondary.value.name = e.name
      languageSecondary.value.label = e.label
      languageSecondary.value.search = ''
      emit('update:secondaryLanguage', languageSecondary.value)
    }
    const handleToggleProductIcon = (val) => {
      data.value.widget_real_product_icon = val
    }
    const handleToggleGA4 = (val) => {
      data.value.is_enable_google_analytic = val
      if (val == 0) data.value.google_analytic_code = ''
    }
    const handleToggleSearch = (val) => {
      data.value.widget_is_enable_search_bar = val
    }
    const handleToggleRemoveBranding = () => {
      if(showBuybackBranding.value == 1){
        if(props.isPaywalledWidgetWatermark) return openPaywall({
          title: 'Remove Powered by Reusely',
          description: '',
          image_url: '/images/paywall/paywall-widget-remove-branding.svg',
          package_name: 'Growth'
        })
        // if(props.brandFree) {
        //   modal.value = true
        //   return
        // }
      }
      showBuybackBranding.value = showBuybackBranding.value == 1 ? 0 : 1
      emit('update:removeBrand')
    }
    const handleToggleBuybackValue = (val) => {
      data.value.widget_is_enable_get_up_to = val
    }
    const handleTogglePriceTrendGraph = async (val) => {
      if (val === '1') {
        const validTitle = await validationGraph.value.title.$validate()
        const validCurrent = await validationGraph.value.current.$validate()
        const validExpected = await validationGraph.value.expected.$validate()
        const isValid = await validationGraph.value.$validate()
        if (validTitle && validCurrent && validExpected) {
          data.value.widget_is_enable_price_trend_graph = dataTrendGraph.enable
        } else dataTrendGraph.enable = val === '1' ? '0' : '1'
      } else data.value.widget_is_enable_price_trend_graph = dataTrendGraph.enable
    }
    const handleChangeGraphTextTitle = async () => {
      const isValid = await validationGraph.value.title.$validate()
      if (isValid)
        data.value.widget_price_trend_graph_title = dataTrendGraph.title
    }
    const handleChangeGraphTextCurrent = async () => {
      const isValid = await validationGraph.value.current.$validate()
      if (isValid)
        data.value.widget_price_trend_graph_current = dataTrendGraph.current
    }
    const handleChangeGraphTextExpected = async () => {
      const isValid = await validationGraph.value.expected.$validate()
      if (isValid)
        data.value.widget_price_trend_graph_next_week = dataTrendGraph.expected
    }
    const condition = computed((someCondition) => {
      return someCondition ? '' : '0'
    })
    const isSearchDisabled = computed(() => {
      return data.value.is_widget_enable_brand_step === '3'
    })

    watch(() => ({
      enable: data.value.widget_is_enable_price_trend_graph,
      title: data.value.widget_price_trend_graph_title,
      current: data.value.widget_price_trend_graph_current,
      expected: data.value.widget_price_trend_graph_next_week 
    }), (val) => {
      dataTrendGraph.enable = val.enable
      dataTrendGraph.title = val.title
      dataTrendGraph.current = val.current
      dataTrendGraph.expected = val.expected
    })

    return {
      data,
      dataTrendGraph,
      modal,
      showCode,
      language,
      languages,
      computedCode,
      isRealPorductIcon,
      languageSecondary,
      languagesSecondary,
      showBuybackBranding,
      validationGraph,
      handleCopy,
      handleToggleGA4,
      handleOpenBilling,
      handlePrimaryColor,
      handleSelectButton,
      handleBackgroundColor,
      handleRemoveBranding,
      handleChangeLanguage,
      handleToggleProductIcon,
      handleToggleRemoveBranding,
      handleChangeLanguageSecondary,
      handleToggleBuybackValue,
      handleTogglePriceTrendGraph,
      handleChangeGraphTextTitle,
      handleChangeGraphTextCurrent,
      handleChangeGraphTextExpected,
      handleToggleSearch,
      condition,
      isSearchDisabled,
    }
  }
})
</script>

<style scoped lang="scss">
.widget-secondary-color {
  .widget-secondary-color__option {
    &.--selected {
      @apply border-teal-04;
    }
  }
}
</style>
