<template>
  <WidgetPrefix
    ref="widgetPrefix"
    v-model="data" :loading="loading"
  />
  <WidgetPurchasing
    ref="widgetPurchasing"
    v-model="data"
    :loading="loading"
    :static-data="staticData"
    :proplan="proplan"
    :white-label="whiteLabel"
    @update:message="handleSaveMessage"
    @update:customButton="handleSaveCustomButton"
  />
  <WidgetLeads
    ref="widgetLeads"
    v-model="data"
  />
  <WidgetPriceLock
    ref="widgetPirceLock"
    v-model="data"
    :loading="loading"
  />
  <WidgetLimitItems 
    ref="widgetLimitItems"
    v-model="data"
    :loading="loading"
  />
  <WidgetImeiSn
    ref="widgetImeiSn"
    v-model="data"
    :loading="loading"
  />
</template>
<script>
import { computed, defineComponent, ref } from 'vue'

import WidgetPurchasing from 'components/Widget/Controller/Purchasing/index.vue'
import WidgetLimitItems from 'components/Widget/Controller/LimitItems/index.vue'
import WidgetPriceLock from 'components/Widget/Controller/PriceLock/index.vue'
import WidgetPrefix from 'components/Widget/Controller/Prefix/index.vue'
import WidgetImeiSn from 'components/Widget/Controller/ImeiSn/index.vue'
import WidgetLeads from 'components/Widget/Controller/Leads/index.vue'
import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  name: 'WidgetOffer',
  components: {
    WidgetPurchasing,
    WidgetLimitItems,
    WidgetPriceLock,
    WidgetPrefix,
    WidgetImeiSn,
    WidgetLeads,
    Fieldset,
    Button
  },
  props: {
    modelValue: {
      type: Object,
      default: undefined
    },
    staticData: {
      type: Object,
      default: undefined
    },
    loading: {
      type: Object,
      default: () => ({})
    },
    proplan: {
      type: Boolean,
      default: false
    },
    whiteLabel: {
      type: Boolean,
      default: false
    }
  },
  emits: [
    'update:modelValue',
    'update:message',
    'update:customButton'
  ],
  setup(props, { emit }) {
    const widgetPrefix = ref()
    const widgetPurchasing = ref()
    const widgetLeads = ref()
    const widgetPirceLock = ref()
    const widgetImeiSn = ref()
    const widgetLimitItems = ref()
    const data = computed({
      get: () => props.modelValue,
      set: (value) => emit('update:modelVallue', value)
    })

    const handleSaveMessage = (val) => emit('update:message', val)
    const handleSaveCustomButton = () => emit('update:customButton')

    return {
      data,
      widgetPrefix,
      widgetPurchasing,
      widgetLeads,
      widgetPirceLock,
      widgetImeiSn,
      widgetLimitItems,
      handleSaveMessage,
      handleSaveCustomButton
    }
  }
})
</script>
