<template>
  <FloatingSheets class="mt-1 mb-8" block cover-width>
    <template #reference="{ openSheet }">
      <label>Product</label>
      <Input
        v-model="search"
        placeholder="Search Brands, Models, or Categories"
        @input="handleSearch"
        @focus-in="openSheet"
        @change="handleReset"
        block
      />
    </template>
    <template #default="{ closeSheet }">
      <div
        v-loading="loading.custom_starting_point"
        class="px-6 py-4"
        :style="{
          maxHeight: '250px',
          overflowY: 'auto'
        }">
        <template v-if="isEmpty">
          <p>Nothing to show</p>
        </template>
        <template v-else>
          <div
            v-for="[categoryType, categoryItems] of Object.entries(customStarting)"
            :key="categoryType">
            <p
              v-if="categoryItems.length > 0"
              class="font-semibold mb-2">
              {{ categoryType }}
            </p>
            <div
              v-for="category in categoryItems"
              :key="category.slug"
              class="py-3 px-4 hover:bg-grey-lighten cursor-pointer"
              @click="() => {
                closeSheet()
                handleSelect(
                  category,
                  categoryType
                )
              }">
              <p>{{ category.name }}</p>
            </div>
          </div>
        </template>
      </div>
    </template>
  </FloatingSheets>
  <Fieldset v-if="selected && selected.is_mac && selected.type === 'model'" class="rounded-md mb-8">
    <template #label>
      Combination
    </template>
    <template #default>
      <Combination
        :selected-product="selected"
        @combine="handleTokenCombination"
      />
    </template>
  </Fieldset>
  <Fieldset class="mb-8 rounded-md">
    <template #label>
      Location
    </template>
    <template #default>
      <Locations
        v-model="location"
        :selected-product="selected"
        @update:modelValue="handleSelectLocation"
      />
    </template>
  </Fieldset>
  <Transition name="fade">
    <WidgetCode
      v-if="showCode"
      v-model="computedCode"
      title="Unique Widget Code"
      type="unique"
    />
  </Transition>

  <!-- <Modal
    v-model="modalWidget.custom_starting_point"
    size="xs"
    hide-footer>
    <Alert color="blue" view="smooth" class="mb-5" dense>
      <div class="flex items-center h-full">
        You can create an unlimited amount of these, the widget code will update
        as you select the starting point.
      </div>
      <template #left>
        <span class="material-icons text-4xl">info</span>
      </template>
    </Alert>
    <p class="my-4">
      Choose your widget starting point, use this to create custom landing
      pages.
    </p>
    <p class="mb-8">
      <span class="font-semibold">For example:</span> if you are building a
      campaign to purchase iPhones you will start your widget on the iPhone
      landing page
    </p>
  </Modal> -->
</template>

<script>
import { computed, defineComponent, ref } from 'vue'
import { useWidget } from 'composable/setting'
import debounce from 'utils/debounce'

import WidgetCode from 'components/Widget/Controller/Code/index.vue'
import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import FloatingSheets from 'components/FloatingSheets.vue'
import Input from 'bbUi/components/input/input.vue'
import Locations from './Locations.vue'
import Combination from './Combination.vue'

export default defineComponent({
  name: 'WidgetLanding',
  components: {
    FloatingSheets,
    WidgetCode,
    Locations,
    Fieldset,
    Input,
    Combination
  },
  props: {
    modelValue: {
      type: Object,
      default: undefined
    },
    code: {
      type: String,
      default: undefined
    },
    customStarting: {
      type: Object,
      default: undefined
    }
  },
  emits: ['update:modelValue', 'select', 'select-location', 'combine'],
  setup(props, { emit }) {
    const { loading, actionCustomStartingPoint } = useWidget() 
    const search = ref(null)
    const selected = computed({
      get: () => props.modelValue,
      set: (value) => emit('update:modelValue', value)
    })
    const isEmpty = computed(() =>
      !props.customStarting?.Brands?.length &&
      !props.customStarting?.Categories?.length &&
      !props.customStarting?.Devices?.length &&
      !props.customStarting?.Models?.length
    )
    const showCode = computed(() => {
      // if (!props.modelValue || !props.modelValue.search || props.modelValue.search === '') {
      //   return false
      // }


      if (
        !!props.modelValue.brand_slug ||
        !!props.modelValue.category_slug ||
        !!props.modelValue.device_slug ||
        !!props.modelValue.token_model ||
        !!props.modelValue.token_model_combine ||
        !!props.modelValue.token_location) {
        return true
      }

      return false
    })
    const computedCode = computed(() => {
      let scriptUniqueCode = props.code
      try {
        let lines = scriptUniqueCode.split('\n')
        let findkey = undefined
        lines.forEach((item, key) => {
          if (item.includes('disableFloatButton')) {
            findkey = {
              value: item,
              key
            }
            return
          }
        })
        const replacer = (slug, value) => {
          return lines.splice(
            findkey.key,
            0,
            findkey.value.replace(
              'disableFloatButton: false',
              `${slug}: "${value}",`
            ))
        }

        if (findkey) {
          if (props.modelValue.device_slug)
            replacer('deviceSlug', props.modelValue.device_slug)
          if (props.modelValue.brand_slug)
            replacer('brandSlug', props.modelValue.brand_slug)
          if (props.modelValue.category_slug)
            replacer('categorySlug', props.modelValue.category_slug)
          if (props.modelValue.token_model)
            replacer('tokenModel', props.modelValue.token_model_combine || props.modelValue.token_model)
          if (props.modelValue.token_location)
            replacer('tokenLocation', props.modelValue.token_location)
        }

        scriptUniqueCode = lines.join('\n')
      } catch (err) {
        console.error(`Error generating widget code: ${err}`)
      }

      return scriptUniqueCode
    })
    const location = ref()

    const handleReset = () => {
      if (search.value === '') {
        emit('update:modelValue', {
          device_slug: null,
          brand_slug: null,
          category_slug: null,
          token_model: null,
          token_location: null,
          search: ''
        })
      }
    }
    const handleSearch = debounce(async () => {
      await actionCustomStartingPoint({ search: search.value })
    }, 500)
    const handleSelect = (category, categoryType) => {
      search.value = category.name
      emit('select', category, categoryType)
    }
    const handleSelectLocation = (tokenLocation) => {
      emit('select-location', {
        token_location: tokenLocation
      })
    }
    const handleTokenCombination = (token) => {
      emit('combine', token)
    }

    return {
      search,
      loading,
      isEmpty,
      showCode,
      selected,
      location,
      computedCode,
      handleReset,
      handleSearch,
      handleSelect,
      handleSelectLocation,
      handleTokenCombination
    }
  }
})
</script>