<template>
  <Fieldset class="mb-6 rounded-xl">
    <template #label>Collect Leads</template>
    <template #default>
      <div>
        <Checkbox
          v-model="data.is_enable_collectleads"
          type="toggle"
          :value="'1'"
          :value-off="'0'">
          <Tooltip
            trigger="hover"
            content="Generate more leads by collecting customer information before they can view their offer."
            placement="top">
            <div class="flex items-center justify-center">
              <label class="mr-2">Lead Booster</label>
              <i class="material-icons text-teal-04 cursor-pointer">
                rocket_launch
              </i>
            </div>
          </Tooltip>
        </Checkbox>
      </div>
    </template>
  </Fieldset>
</template>

<script>
import { computed, defineComponent } from 'vue'

import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'

export default defineComponent({
  name: 'WidgetLeads',
  components: {
    Fieldset,
    Checkbox
  },
  props: {
    modelValue: {
      type: Object,
      default: undefined
    },
    code: {
      type: String,
      default: undefined
    },
    customStarting: {
      type: Object,
      default: undefined
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    const data = computed({
      get: () => props.modelValue,
      set: (value) => emit('update:modelValue', value)
    })

    return {
      data
    }
  }
})
</script>