<template>
  <CollapsibleCard :disabled="disabledWidget" max-height="none" class="mb-6">
    <template #title>
      <h2 :class="disabledWidget && 'text-grey-02'" class="text-xl font-medium">Widget Code</h2>
    </template>
    <template #default>
      <WidgetStartingPoint
        ref="widgetStartingPoint"
        v-model="data"
        :loading="loading"
        :proplan="proplan"
        :white-label="whiteLabel"
      />
      <WidgetCode
        ref="widgetCode"
        v-model="code"
        :white-label="whiteLabel"
        type="base" 
      />
      <WidgetHost
        ref="widgetHost"
        v-model="data.widget_link"
        :white-label="whiteLabel"
      />
    </template>
  </CollapsibleCard>
  <CollapsibleCard
    v-loading="loading.sort_order"
    :disabled="disabled.sortOrder || disabledWidget"
    max-hieght="none"
    class="mb-6">
    <template v-if="sortOrder" #title>
      <Tooltip v-if="!sortOrder.data.length" placement="top">
        <template #content>
          <div class="max-w-xs">
            You must enable and price at least one product before using this feature.
          </div>
        </template>
        <div class="flex items-center gap-x-2">
          <i v-if="!disabledWidget" class="text-orange-04 material-icons cursor-pointer">warning</i>
          <h2 class="text-xl text-grey-02">Product Sort Order</h2>
        </div>
      </Tooltip>
      <h2 v-else class="text-xl font-medium">Product Sort Order</h2>
    </template>
    <template v-else #title>
      <h2 class="text-xl">Product Sort Order</h2>
    </template>
    <template #default="scope">
      <WidgetSortOrder
        ref="widgetSortOrder"
        v-model="sortOrder"
        :open="scope.open"
        :custom-starting="landing"
        :current-starting="data.is_widget_enable_brand_step"
        @update:sortOrder="handleUpdateSort"
      />
    </template>
  </CollapsibleCard>
  <CollapsibleCard :disabled="disabledWidget" max-height="none" class="mb-6">
    <template #title>
      <div class="flex items-center">
        <h2 :class="disabledWidget && 'text-grey-02'" class="text-xl font-medium">Landing Page Builder</h2>
        <Tooltip :disabled="disabledWidget" placement="top">
          <template #content>
            <div class="w-80">This setting creates a unique widget code that only displays a specific category, brand, or model of products. This is generally used for landing pages on a website, for example, an iPhone landing page would utilize this feature.</div>
          </template>
          <template #default>
            <i v-if="!disabledWidget" class="text-teal-04 material-icons ml-2 cursor-pointer">info</i>
            <i v-else />
          </template>
        </Tooltip>
      </div>
    </template>
    <template #default>
      <WidgetLanding
        ref="widgetLanding"
        v-model="landing"
        :custom-starting="customStarting"
        :code="code"
        @select="handleSelectLanding"
        @select-location="handleSelectLocation"
        @combine="handleTokenCombination"
        @update:landingPage="handleUpdateLanding"
      />
    </template>
  </CollapsibleCard>
  <CollapsibleCard :disabled="disabledWidget" max-height="none" class="mb-6">
    <template #title>
      <h2 :class="disabledWidget && 'text-grey-02'" class="text-xl font-medium">Widget Settings</h2>
    </template>
    <template #default>
      <WidgetSetting
        ref="widgetSetting"
        v-model="data"
        :loading="loading"
        :proplan="proplan"
        :brand="removeBrand"
        :brandFree="removeBrandFree"
        :white-label="whiteLabel"
        :is-paywalled-widget-watermark="isPaywalledWidgetWatermark"
        :code="code"
        :landing="landing"
        @update:removeBrand="handleRemoveBrand"
        @update:language="handleChangeLanguage"
        @update:secondaryLanguage="handleChangeLanguageSecondary"
      />
    </template>
  </CollapsibleCard>  
  <CollapsibleCard :disabled="disabledWidget" :is-collapse="isCollapseOfferSetting" max-height="none" class="mb-6">
    <template #title>
      <h2 :class="disabledWidget && 'text-grey-02'" class="text-xl font-medium">Offer Settings</h2>
    </template>
    <template #default>
      <WidgetOffer
        ref="widgetOffer"
        v-model="data"
        :loading="loading"
        :static-data="staticData"
        :proplan="proplan"
        :white-label="whiteLabel"
        @update:message="handleSaveMessage"
        @update:customButton="handleSaveCustomButton"
      />
    </template>
  </CollapsibleCard>
</template>
<script>
import { defineComponent, computed, ref } from 'vue'

import WidgetStartingPoint from 'components/Widget/Controller/StartingPoint/index.vue'
import WidgetCode from 'components/Widget/Controller/Code/index.vue'
import WidgetHost from 'components/Widget/Controller/Host/index.vue'
import WidgetSortOrder from 'components/Widget/Controller/SortOrder/index.vue'
import WidgetLanding from 'components/Widget/Controller/Landing/index.vue'
import WidgetSetting from 'components/Widget/Controller/Setting/index.vue'
import WidgetPurchasing from 'components/Widget/Controller/Purchasing/index.vue'
import WidgetConfirmationMessage from 'components/Widget/Controller/ConfirmationMessage/index.vue'
import WidgetOffer from 'components/Widget/Controller/Offer/index.vue'
import CollapsibleCard from 'components/CollapsibleCard.vue'

export default defineComponent({
  name: 'WidgetController',
  components: {
    WidgetStartingPoint,
    WidgetCode,
    WidgetHost,
    WidgetSortOrder,
    WidgetLanding,
    WidgetSetting,
    WidgetPurchasing,
    WidgetConfirmationMessage,
    WidgetOffer,
    CollapsibleCard,
  },
  props: {
    modelValue: {
      type: Object,
      default: () => ({})
    },
    proplan: {
      type: Boolean,
      default: false
    },
    source: {
      type: String,
      default: undefined
    },
    staticData: {
      type: Object,
      default: undefined
    },
    sortOrder: {
      type: Object,
      default: undefined
    },
    customStarting: {
      type: Object,
      default: undefined
    },
    loading: {
      type: Object,
      default: () => ({})
    },
    removeBrand: {
      type: Boolean,
      default: false
    },
    removeBrandFree: {
      type: Boolean,
      default: false
    },
    collapsedSection: {
      type: Array,
      default: () => ([])
    },
    whiteLabel: {
      type: Boolean,
      default: false
    },
    disabledWidget: {
      type: Boolean,
      default: false
    },
    isPaywalledWidgetWatermark: {
      type: Boolean,
      default: false
    }
  },
  emits: [
    'update:message',
    'update:modelValue',
    'update:sortOrder',
    'update:language',
    'update:landingPage',
    'update:removeBrand',
    'update:customButton',
    'update:secondaryLanguage'
  ],
  setup(props, { emit }) {
    const data = computed({
      get: () => props.modelValue,
      set: (value) => emit('update:modelValue', value)
    })
    const code = computed({
      get: () => props.source,
      set: () => props.source
    })
    const landing = ref({})
    const widgetStartingPoint = ref()
    const widgetCode = ref()
    const widgetHost = ref()
    const widgetSortOrder = ref()
    const widgetLanding = ref()
    const widgetSetting = ref()
    const widgetOffer = ref()
    const loading = computed(() => props.loading)
    const disabled = computed(() => ({
      get sortOrder() {
        if (!props.sortOrder) return true
        if (!props.sortOrder.data.length) return true
        return false
      }
    }))
    const isCollapseOfferSetting = computed(() => props.collapsedSection.includes('offer'))

    const handleUpdateSort = () => emit('update:sortOrder')
    const handleUpdateLanding = (value) => emit('update:landingPage', value)
    const handleSelectLanding = (category, categoryType) => {
      landing.value = {
        device_slug: null,
        brand_slug: null,
        category_slug: null,
        token_model: null,
        token_location: null,
        search: ''
      }

      const {
        name,
        slug,
        brand: brandSlug = null,
        token_model: tokenModel,
        token_location: tokenLocation,
        device
      } = category

      if (tokenLocation) {
        landing.value.token_location = tokenLocation
      } else if (tokenModel) {
        landing.value.token_model = tokenModel
      } else if (categoryType === 'Brands') {
        landing.value.brand_slug = slug
      } else if (categoryType === 'Categories') {
        landing.value.category_slug = slug
      } else {
        landing.value = {
          device_slug: slug,
          brand_slug: brandSlug
        }
      }
      landing.value.type = category.type || category.brand
      landing.value.custom_starting_value = category.custom_starting_value
      landing.value.search = name
      landing.value.is_mac = category.is_mac == 1 ? 1 : (device === 'Mac' ? 1 : 0)
      handleUpdateLanding(landing.value)
      widgetSortOrder.value.actionShowSortOrder()
    }
    const handleSelectLocation = (value) => {
      landing.value = { ...landing.value, ...value }
      handleUpdateLanding(landing.value)
    }
    const handleRemoveBrand = () => emit('update:removeBrand')
    const handleChangeLanguage = (val) => emit('update:language', val)
    const handleChangeLanguageSecondary = (val) => emit('update:secondaryLanguage', val)
    const handleSaveMessage = (val) => emit('update:message', val)
    const handleSaveCustomButton = () => emit('update:customButton')
    const handleTokenCombination = (token) => {
      landing.value = { ...landing.value, token_model_combine: token }
      handleUpdateLanding(landing.value)
    }

    return {
      data,
      code,
      landing,
      loading,
      disabled,
      widgetStartingPoint,
      widgetCode,
      widgetHost,
      widgetSortOrder,
      widgetLanding,
      widgetSetting,
      widgetOffer,
      isCollapseOfferSetting,
      handleUpdateSort,
      handleRemoveBrand,
      handleSaveMessage,
      handleUpdateLanding,
      handleSelectLanding,
      handleSelectLocation,
      handleChangeLanguage,
      handleSaveCustomButton,
      handleTokenCombination,
      handleChangeLanguageSecondary
    }
  }
})
</script>
