<template>
    <Fieldset class="mb-6 rounded-xl">
      <template #label>Mail-In Item Limit</template>
      <template #default>
        <div class="flex flex-row p-4 bg-blue-01 text-blue-04 rounded-xl mb-4 select-none">
          <i class="material-icons mr-2">info</i>
          <p class="mt-1 text-sm">
            <b>Restrict the number of items</b>.
            You can set limit on items per offer exclusively for the Mail-in purchasing method.
          </p>
        </div>
        <form autocomplete="off" @submit.prevent="handleSave">
        <Input
          v-loading="loading.all"
          v-model="form.widget_limit_qty_items"
          v-maska="'##'"
          :disabled="loading.update"
          class="input-limit-items"
          autocomplete="off"
          :view="(validation.widget_limit_qty_items.$invalid && 'danger') || 'default'"
          icon-block
          right
          icon>
          <template #icon>
            <span class="text-sm">/Items</span>
          </template>
        </Input>
        <small
          class="text-red-base block"
          v-for="(error, errorIndex) of validation.widget_limit_qty_items.$errors"
          :key="errorIndex">
          {{ error.$message }}.
        </small>
        <small class="block mt-4">
          <span class="font-semibold">Max Items</span>: Up to 99 items
        </small>
        <div class="mt-4 flex justify-end">
          <Button
            color="teal"
            dense
            type="submit"
            @click="handleSave">
            Save & Apply
          </Button>
        </div>
      </form>
    </template>
    </Fieldset>
</template>

<script setup>
import { computed, defineProps, reactive, watch } from 'vue'
import { maxValue, minValue, required } from '@vuelidate/validators'
import useVuelidate from '@vuelidate/core'
import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'

const props = defineProps({
  modelValue: {
    type: Object,
    default: () => ({})
  },
  loading: {
    type: Object,
    default: () => ({})
  }
})

const emit = defineEmits(['update:modelValue'])

const data = computed({
  get: () => props.modelValue,
  set: (value) => emit('update:modelVallue', value)
})

const form = reactive({
  widget_limit_qty_items: null
})

const rules = {
    widget_limit_qty_items: {
        required,
        minValue: minValue(1),
        maxValue: maxValue(99)
    }
}
const validation = useVuelidate(rules, form, {
  $lazy: true
})

const handleSave = async () => {
  const valid = await validation.value.$validate()
  if(!valid) return
  data.value.widget_limit_qty_items = form.widget_limit_qty_items
}

  watch(
      () => data.value.widget_limit_qty_items,
      (val) => {
        if (val) form.widget_limit_qty_items = val
      },
      { immediate: true, deep: true }
    )
</script>

<style lang="scss">
.input-limit-items {
  @apply w-full ;

  input {
    @apply text-right xl:text-left;
    padding-right: 5rem !important;
  }
}
</style>