<template>
  <Fieldset class="mb-6 rounded-xl">
    <template #label>Widget URL</template>
    <template #default>
      <Alert color="blue" view="smooth" class="mb-3">
        <div class="-mx-2 -my-1 flex items-start text-sm gap-x-2">
          <i class="material-icons">error</i>
          <span>Please ensure that you provide the Widget URL correctly.</span>
        </div>
      </Alert>
      <Input
        v-model="link"
        label="Add the URL your widget is being hosted on"
        placeholder="Widget URL"
        block
      />
    </template>
  </Fieldset>
</template>
<script>
import { computed, defineComponent } from 'vue'

import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Alert from 'bbUi/components/alert/alert.vue'
import Input from 'bbUi/components/input/input.vue'

export default defineComponent({
  name: 'WidgetHost',
  components: {
    Fieldset,
    Alert,
    Input
  },
  props: {
    modelValue: {
      type: String,
      default: undefined
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    const link = computed({
      get: () => props.modelValue,
      set: (value) => emit('update:modelValue', value)
    })

    return {
      link
    }
  }
})
</script>