/**
 * Lazy function that returns the format of a given color object.
 *
 * Doesn’t handle invalid formats.
 */
export function detectFormat (color) {
	if ('r' in color) {
		return 'rgb'
	}

	if ('w' in color) {
		return 'hwb'
	}

	if ('v' in color) {
		return 'hsv'
	}

	if ('s' in color) {
		return 'hsl'
	}

	return null
}