<template>
    <Fieldset class="mb-6 rounded-xl">
      <template #label>IMEI/SN Input Section</template>
      <template #default>
        <form
          autocomplete="off"
          @submit.prevent="handleSave">
          <div class="grid grid-cols-1 gap-4">
            <div class="flex items-center">
                <Checkbox
                id="widget_is_enable_imei_input"
                v-model="form.widget_is_enable_imei_input"
                :loading="loading.all"
                :disabled="loading.update || disabledImei"
                type="toggle"
                :value="'1'"
                :value-off="'0'">
                IMEI/SN Input Section
                </Checkbox>
                <Tooltip placement="top">
                    <template #content>
                        <div class="w-80">
                            {{ disabledImei ? 'IMEI/SN settings are only available for electronics. Please enable electronics in your catalog to modify this setting.' : 'This setting applies only to electronics and products with IMEI/SN. If no electronics are enabled in the catalog, this setting will be disabled.' }}
                        </div>
                    </template>
                    <template #default>
                        <i class="material-icons">info</i>
                    </template>
                </Tooltip>
            </div>
  
            <div class="ml-8" v-if="form.widget_is_enable_imei_input == '1'">
                <Checkbox
                  v-model="form.widget_is_enable_imei_ins_msg"
                  :loading="loading.all"
                  :disabled="loading.update"
                  type="toggle"
                  :value="'1'"
                  :value-off="'0'">
                  Instructional Message
                </Checkbox>
  
                <template v-if="form.widget_is_enable_imei_ins_msg == '1'">
                  <div class="mt-4">
                    <label class="text-sm font-semibold block">Text for Checkout Page Section</label>
                    <TextEditor
                     ref="refTextEditor"
                     v-model="form.widget_imei_ins_msg" 
                     :options="ckeditor"
                     :toolbarItems="toolbarItems" 
                    />
                  </div>
                      <div class="mt-4">
                        <label class="text-sm font-semibold block">Icon</label>
                        <div class="sm:w-1/2 xl:w-full grid grid-cols-3 gap-8 xl:gap-6 mt-4">
                            <button 
                            type="button"
                            v-for="option in iconOptions" 
                            :key="option.value"
                            class="p-2 rounded-xl"
                            :class="form.widget_imei_icon_selected === option.value ? 'border-4 border-teal-04' : 'border border-grey-01'" 
                            @click.prevent="selectIcon(option.value)"
                            :disabled="loading.update"
                            >
                            <img class="mx-auto" :src="option.icon" :alt="option.icon">
                        </button>
                    </div>
                </div>
            </template>
            </div>
            
            <Button
            color="teal"
            dense
            class="w-full"
            type="submit"
            @click="handleSave"
            :loading="loading.update"
            >
                    Apply
                </Button>
            </div>
            </form>
        </template>
    </Fieldset>
  </template>
  
  <script>
  import { computed, defineComponent, reactive, ref, watch } from 'vue';
  
  import Fieldset from 'bbUi/components/fieldset/fieldset.vue';
  import Checkbox from 'bbUi/components/checkbox/checkbox.vue';
  import Button from 'bbUi/components/button/button.vue';
  import { Editor as ClassicEditor } from '@erdeprsty/ckeditor5-custom-build'
  import TextEditor from 'components/TextEditor.vue'
  
  export default defineComponent({ 
    name: 'WidgetImeiSn',
    components: {
      Fieldset,
      Checkbox,
      Button,
      TextEditor
    },    
    props: {
      modelValue: {
        type: Object,
        required: true
      },
      loading: {
        type: Object,
        default: () => ({})
      }
    },
    emits: ['update:modelValue'],
    setup(props, { emit }) {
      const iconOptions = [
        { 
          icon: 'https://d18hvo5uozp8li.cloudfront.net/public/images/electric_bolt.svg',
          value: 'https://d18hvo5uozp8li.cloudfront.net/public/images/electric_bolt.svg'
        },
        { 
          icon: 'https://d18hvo5uozp8li.cloudfront.net/public/images/speed.svg',
          value: 'https://d18hvo5uozp8li.cloudfront.net/public/images/speed.svg' 
        },
        {
          icon: 'https://d18hvo5uozp8li.cloudfront.net/public/images/quickreply.svg',
          value: 'https://d18hvo5uozp8li.cloudfront.net/public/images/quickreply.svg'
        },
        {
          icon: 'https://d18hvo5uozp8li.cloudfront.net/public/images/warning-icon.svg',
          value: 'https://d18hvo5uozp8li.cloudfront.net/public/images/warning-icon.svg'
        },
        {
          icon: 'https://d18hvo5uozp8li.cloudfront.net/public/images/chat_bubble_outline.svg',
          value: 'https://d18hvo5uozp8li.cloudfront.net/public/images/chat_bubble_outline.svg'
        },
        {
          icon: 'https://d18hvo5uozp8li.cloudfront.net/public/images/motion_photos_off.svg',
          value: ''
        }
      ];
      const refTextEditor = ref(null);
      const ckeditor = reactive({
        editor: ClassicEditor,
        config: {
            htmlSupport: {
            allow: [{
                styles: true,
                classes: true
            }]
          }
         }
       })
       const toolbarItems = [
                'undo',
                'redo',
                'link',
                'horizontalLine',
                'specialCharacters',
                'heading',
                '|',
                'bold',
                'italic',
                'underline',
                'strikethrough',
                {
                  label: 'Basic styles',
                  icon: 'text',
                  items: [
                    'fontSize',
                    'fontFamily',
                    'fontColor',
                    'fontBackgroundColor',
                    'highlight',
                    'superscript',
                    'subscript',
                    'code',
                    '|',
                    'textPartLanguage',
                    '|'
                  ]
                },
                'removeFormat',
                '|',
                'alignment',
                '|',
                'bulletedList',
                'numberedList',
                '|',
                'outdent',
                'indent'
              ]
  
      const data = computed({
        get: () => props.modelValue,
        set: (value) => emit('update:modelValue', value)
      });

      const disabledImei = computed(() => data.value.widget_auto_disabled_imei);
  
      const form = reactive({
        widget_is_enable_imei_input: data.value.widget_is_enable_imei_input || '0',
        widget_is_enable_imei_ins_msg: data.value.widget_is_enable_imei_ins_msg || '0',
        widget_imei_ins_msg: data.value.widget_imei_ins_msg || '',
        widget_imei_icon_selected: data.value.widget_imei_icon_selected || ''
      });
  
      const selectIcon = (value) => {
        form.widget_imei_icon_selected = value;
      };
  
      const handleSave = () => {
        data.value.widget_is_enable_imei_input = form.widget_is_enable_imei_input;
        data.value.widget_is_enable_imei_ins_msg = form.widget_is_enable_imei_ins_msg;
        data.value.widget_imei_ins_msg = form.widget_imei_ins_msg;
        data.value.widget_imei_icon_selected = form.widget_imei_icon_selected;
      };
  
      watch(
        () => props.modelValue,
        (newVal) => {
          if (newVal) {
            form.widget_is_enable_imei_input = newVal.widget_is_enable_imei_input || form.widget_is_enable_imei_input;
            form.widget_is_enable_imei_ins_msg = newVal.widget_is_enable_imei_ins_msg || form.widget_is_enable_imei_ins_msg;
            form.widget_imei_ins_msg = newVal.widget_imei_ins_msg || form.widget_imei_ins_msg;
            form.widget_imei_icon_selected = newVal.widget_imei_icon_selected || form.widget_imei_icon_selected;
          }
        },
        { immediate: true, deep: true }
      );
  
      return {
        iconOptions,
        form,
        selectIcon,
        handleSave,
        loading: props.loading,
        ckeditor,
        refTextEditor,
        disabledImei,
        toolbarItems
      };
    }
  });
</script>

<style class="scoped"> 
    .ck-editor__editable {
      min-height: 150px;
      height: 150px;
    }
</style>