<template>
  <fieldset v-loading="loading.sort_order" class="fieldset rounded-xl">
    <legend class="fieldset__label">
      Reordered by {{ startingPointText }}
    </legend>
    <div class="py-2">
      <DragList
        v-if="sortOrder"
        v-model="sortOrder.data"
        @opened="handleCollapseSort"
        @change="handleSortingLevelOne"
        @change-child="handleSortingLevelTwo"
      />
    </div>
  </fieldset>
</template>
<script>
import { computed, defineComponent, ref, watch } from 'vue'
import { useWidget } from 'composable/setting'

import DragList from 'components/DragList.vue'

export default defineComponent({
  name: 'WidgetSortOrder',
  components: { DragList },
  props: {
    modelValue: {
      type: Object,
      default: undefined
    },
    customStarting: {
      type: [Object],
      default: undefined
    },
    currentStarting: {
      type: [Number, String],
      default: 0
    },
    open: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue', 'update:sortOrder'],
  async setup(props, { emit }) {
    const sortOrder = computed(() => props.modelValue || undefined)
    const startingPointText = computed(() => {
      if (props.customStarting.brand_slug ||
          props.customStarting.category_slug ||
          props.customStarting.device_slug ||
          props.customStarting.token_model) {
        return props.customStarting.search
      }
      if (props.currentStarting == 0) return 'Product'
      if (props.currentStarting == 1) return 'Brand'
      if (props.currentStarting == 2) return 'Category'
      return 'Unknown'
    })

    const {
      loading,
      actionShowSortOrder,
      actionShowSortOrderChild,
      updateSortLevelOne,
      updateSortLevelTwo,
    } = useWidget()
    
    const handleOpenentCollapse = async () =>  {
      if (!sortOrder.value || !sortOrder.value.data) {
        return await actionShowSortOrder()
      }
    }
    const handleCollapseSort = async (e) => {
      if (!e.list.length) await actionShowSortOrderChild(e)
    }
    const handleSortingLevelOne = async (e) => {
      if (!sortOrder.value.key) return

      const payload = {
        key: sortOrder.value.key,
        items: e.map((item) => ({
          name: item.name
        }))
      }
      await updateSortLevelOne(payload, () => {
        emit('update:sortOrder')
      })
    }
    const handleSortingLevelTwo = async (e) => {
      if (!e.key) return

      const payload = {
        key: e.key,
        items: e.value.map((item) => ({
          name: item.name
        }))
      }
      await updateSortLevelTwo(payload, () => {
        emit('update:sortOrder')
      })
    }

    await handleOpenentCollapse()

    return {
      loading,
      sortOrder,
      startingPointText,
      actionShowSortOrder,
      handleCollapseSort,
      handleOpenentCollapse,
      handleSortingLevelOne,
      handleSortingLevelTwo
    }
  }
})
</script>