<template>
  <div class="widget-confirm-message">
    <Fieldset
      v-if="data.purchasing_method.value == 1 || data.purchasing_method.value == 2"
      class="rounded-md">
      <template #label>Mail-In</template>
      <template #default>
        <div class="flex gap-x-2">
          <Button
            class="flex-1"
            view="outlined"
            color="teal"
            dense
            @click="handlePreview($event)">
            <div class="flex items-center justify-center gap-x-1">
              <i class="material-icons">visibility</i>
              Preview
            </div>
          </Button>
          <Button
            class="flex-1"
            color="teal"
            dense
            :disabled="
              data.purchasing_method.value == '3' ||
              !data.is_enable_mailin_selling_type
            "
            @click="handleModalConfirmMsgOnline">
            <div class="flex flex-nowrap items-center whitespace-nowrap justify-center gap-x-1">
              <i class="material-icons">edit</i>
              Edit Message
            </div>
          </Button>
        </div>
      </template>
    </Fieldset>
  
    <Fieldset
      v-if="data.purchasing_method.value == 1 || data.purchasing_method.value == 3"
      class="rounded-md">
      <template #label>In-Store</template>
      <template #default>
        <div class="flex gap-x-2">
          <Button
            class="flex-1"
            view="outlined"
            color="teal"
            dense
            @click="handlePreview($event, 0)">
            <div class="flex items-center justify-center gap-x-1">
              <i class="material-icons">visibility</i>
              Preview
            </div>
          </Button>
          <Button
            class="flex-1"
            color="teal"
            dense
            :disabled="data.purchasing_method.value == '2'"
            @click="handleModalConfirmMsg">
            <div class="flex flex-nowrap items-center whitespace-nowrap justify-center gap-x-1">
              <i class="material-icons">edit</i>
              Edit Message
            </div>
          </Button>
        </div>
      </template>
    </Fieldset>
  </div>

  <Modal
    v-model="modal.online"
    title="Confirmation Message (Mail-In)"
    size="md">
    <div>
      <div class="my-4">
        You can customize the message that appears on offer details page
      </div>
      <TextEditor
        v-model="messageOnline"
        :options="ckeditor"
      />
    </div>
    <Table
      v-if="staticData.widget_confirm_msg_shortcode && Array.isArray(staticData.widget_confirm_msg_shortcode)"
      :data="staticData.widget_confirm_msg_shortcode"
      class="mt-6">
      <TableColumn prop="code" label="Code">
        <template #default="scope">
          <div class="flex">
            <span class="mr-2">{{ scope.row.code }}</span>
            <i
              class="material-symbols-outlined text-lg text-teal-04"
              @click="copyText(scope.row.code)">
              content_copy
            </i>
          </div>
        </template>
      </TableColumn>
      <TableColumn prop="description" label="Description" />
    </Table>
    <template #footer>
      <div class="flex justify-end">
        <Button
          color="teal"
          :loading="loading.update"
          @click="handleSave">
          Save
        </Button>
      </div>
    </template>
  </Modal>

  <Modal
    v-model="modal.instore"
    title="Contirmation Message (In-Store)"
    size="md">
    <div>
      <div class="my-4">
        You can customize the message that appears on offer details page
      </div>
      <TextEditor
        v-model="message"
        :options="ckeditor"
      />
    </div>
    <Table
      v-if="staticData.widget_confirm_msg_shortcode && Array.isArray(staticData.widget_confirm_msg_shortcode) "
      :data="staticData.widget_confirm_msg_shortcode"
      class="mt-6">
      <TableColumn prop="code" label="Code">
        <template #default="scope">
          <div class="flex">
            <span class="mr-2">{{ scope.row.code }}</span>
            <i
              class="material-symbols-outlined text-lg text-teal-04"
              @click="copyText(scope.row.code)">
              content_copy
            </i>
          </div>
        </template>
      </TableColumn>
      <TableColumn prop="description" label="Description" />
    </Table>
    <template #footer>
      <div class="flex justify-end">
        <Button
          color="teal"
          :loading="loading.update"
          @click="handleSave">
          Save
        </Button>
      </div>
    </template>
  </Modal>

  <Modal
    v-model="modal.preview"
    title="Preview"
    size="lg"
    no-padding>
    <div class="bg-black bg-opacity-30 py-6 px-12">
      <div class="bg-white rounded-lg p-6">
        <div
          v-loading="loading.message"
          v-html="preview"
        />
      </div>
    </div>
    <template #footer>
      <div class="flex justify-start">
        <a
          href="#"
          class="text-red-04 no-underline hover:text-red-03"
          @click.prevent="modal.preview = false">
          Close
        </a>
      </div>
    </template>
  </Modal>
</template>

<script>
import { computed, defineComponent, reactive, ref, watch } from 'vue'
import { Editor as ClassicEditor } from '@erdeprsty/ckeditor5-custom-build'
import copyText from 'utils/copy'

import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Button from 'bbUi/components/button/button.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import TextEditor from 'components/TextEditor.vue'

export default defineComponent({
  name: 'WidgetConfirmationMessage',
  components: {
    TextEditor,
    Fieldset,
    Button,
    Modal
  },
  props: {
    modelValue: {
      type: Object,
      default: undefined
    },
    staticData: {
      type: Object,
      default: undefined
    },
    loading: {
      type: Object,
      default: () => ({})
    }
  },
  emits: ['update:modelValue', 'update:message'],
  setup(props, { emit }) {
    const data = computed({
      get: () => props.modelValue,
      set: (value) => emit('update:modelValue', value)
    })
    const modal = reactive({
      online: false,
      instore: false,
      preview: false
    })
    const preview = ref({})
    const ckeditor = reactive({
      editor: ClassicEditor,
      config: {
        mediaEmbed: {
          previewsInData: true
        }
      }
    })
    const message = ref(data.value.widget_confirm_msg.instore.message)
    const messageOnline = ref(data.value.widget_confirm_msg.mailin.message)

    const handleSave = async () => {
      emit('update:message', {
        widget_confirm_msg: {
          instore: message.value,
          mailin: messageOnline.value
        }
      })
    }
    const handlePreview = async (e, type = 1) => {
      modal.preview = true
      preview.value = type == 1 ? data.value.widget_confirm_msg.mailin.preview_message : data.value.widget_confirm_msg.instore.preview_message
    }
    const handleModalConfirmMsg = () => modal.instore = true
    const handleModalConfirmMsgOnline = () => modal.online = true

    watch(() => data.value, (value) => {
      message.value = value.widget_confirm_msg.instore.message
      messageOnline.value = value.widget_confirm_msg.mailin.message
    }, {
      immediate: true,
      deep: true
    })
    watch(() => props.loading.update, (val) => {
      if (!val && (modal.instore || modal.online)) {
        modal.instore = false
        modal.online = false
      } 
    })

    return {
      data,
      modal,
      preview,
      ckeditor,
      message,
      messageOnline,
      copyText,
      handleSave,
      handlePreview,
      handleModalConfirmMsg,
      handleModalConfirmMsgOnline
    }
  }
})
</script>

<style lang="scss">
.widget-confirm-message {
  @apply space-y-6;
  fieldset {
    border: 0;
    padding-left: 0;
    padding-right: 0;
  }
}
</style>