<template>
  <Fieldset class="mb-6 rounded-xl">
    <template #label>Price Lock</template>
    <template #default>
      <div class="flex flex-row p-4 bg-blue-01 text-blue-04 rounded-xl mb-4 select-none">
        <i class="material-icons mr-2">info</i>
        <p class="mt-1 text-sm">
          <b>Control the offer lifespan</b>.
          Set the duration your buyback offer remains valid. After [number] days,
          any unused offers will automatically expire.
        </p>
      </div>
      <form autocomplete="off" @submit.prevent="handleSave">
        <Input
          v-loading="loading.all"
          v-model="form.expDays"
          v-maska="'##'"
          :disabled="loading.update"
          :view="(validation.expDays.$invalid && 'danger') || 'default'"
          class="input-price-lock"
          autocomplete="off"
          icon-block
          right
          icon>
          <template #icon>
            <span class="text-sm">/Days</span>
          </template>
        </Input>
        <small
          class="text-red-base block"
          v-for="(error, errorIndex) of validation.expDays.$errors"
          :key="errorIndex">
          {{ error.$message }}.
        </small>
        <small class="block mt-4">
          <span class="font-semibold">Max Days</span>: Up to 90 days
        </small>
        <div class="mt-4 flex justify-end">
          <Button
            color="teal"
            dense
            type="submit"
            @click="handleSave">
            Save & Apply
          </Button>
        </div>
      </form>
    </template>
  </Fieldset>
</template>
<script>
import { computed, defineComponent, reactive, watch } from 'vue'
import { useVuelidate } from '@vuelidate/core'
import { minValue, maxValue, required } from '@vuelidate/validators'

import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Button from 'bbUi/components/button/button.vue'
import Input from 'bbUi/components/input/input.vue'

export default defineComponent({
  name: 'WidgetPriceLock',
  components: {
    Fieldset,
    Button,
    Input
  },
  props: {
    modelValue: {
      type: Object,
      default: undefined
    },
    loading: {
      type: Object,
      default: () => ({})
    }
  },
  emits: ['update:modelValue'],
  setup(props) {
    const data = computed({
      get: () => props.modelValue,
      set: (value) => emit('update:modelVallue', value)
    })
    const form = reactive({
      expDays: null
    })
    const rules = {
      expDays: {
        required,
        minValue: minValue(0),
        maxValue: maxValue(90)
      }
    }
    const validation = useVuelidate(rules, form, {
      $lazy: true
    })

    const handleSave = async () => {
      await validation.value.$validate()
      if (!validation.value.$invalid) {
        data.value.exp_days_pricelock = form.expDays
      }
    }

    watch(
      () => data.value.exp_days_pricelock,
      (val) => {
        if (val) form.expDays = val
      },
      { immediate: true, deep: true }
    )

    return {
      data,
      form,
      validation,
      handleSave
    }
  }
})
</script>

<style lang="scss">
.input-price-lock {
  @apply w-full;

  input {
    @apply text-right xl:text-left;

    padding-right: 5rem !important;
  }
}
</style>
