<template>
  <Fieldset
    :style="{
      ...(noTitle && {
        border: '0 !important',
        padding: '0'
      })
    }"
    class="mb-6 rounded-xl">
    <template v-if="!noTitle" #label>{{ title }}</template>
    <template #default>
      <div>
        <p class="mb-6 text-black w-full">
          Copy the following code and paste it as close to the
          opening
          <code>&lt;head&gt;</code> tag as possible on every page
          you wish to display the buyback widget.
        </p>
        <div class="bg-grey-lighten text-sm mb-4 px-4 py-6 border border-grey-02">
          <code id="code" class="widget-code">{{ codes }}</code>
        </div>
      </div>
      <div class="text-right">
        <Button
          color="teal"
          @click="copyCode"
          dense>
          <i class="material-symbols-outlined mx-1">content_copy</i>
          <span class="mx-1">
            {{ text }}
          </span>
        </Button>
      </div>
    </template>
  </Fieldset>
</template>
<script>
import { computed, defineComponent, onUnmounted, ref } from 'vue'

import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  name: 'WidgetCode',
  components: {
    Fieldset,
    Button
  },
  props: {
    modelValue: {
      type: String,
      default: undefined
    },
    title: {
      type: String,
      default: 'Widget Code'
    },
    noTitle: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: 'base' // base | unique
    }
  },
  setup(props) {
    const text = ref('Copy')
    const codes = computed(() => props.modelValue)
    const timerCopyCode = ref(undefined)

    const copyCode = () => {
      var input = document.body.appendChild(document.createElement('textarea'))
      input.value = codes.value
      input.focus()
      input.select()
      input.setSelectionRange(0, 99999)
      document.execCommand('copy')
      input.parentNode.removeChild(input)
      text.value = 'Copied'
      timerCopyCode.value = setTimeout(() => (text.value = 'Copy'), 3000)
    }

    onUnmounted(() => {
      clearTimeout(timerCopyCode.value)
    })

    return {
      text,
      codes,
      timerCopyCode,
      copyCode
    }
  }
})
</script>

<style lang="scss">
.widget-code {
  overflow-wrap: anywhere;
  white-space: break-spaces;
}
</style>
