<template>
  <Fieldset class="mb-6 rounded-xl">
    <template #label>Terms & Conditions</template>
    <template #default>
      <form>
        <Input 
          label="Terms and Condition Link" 
          class="w-full" 
          v-model="form.widget_term_condition"
          placeholder="link"
          :disabled="loading.update"
          :loading="loading.all"
          @focus-out="handleSave"
          :view="validation.widget_term_condition.$invalid ? 'danger' : 'default'"
        />
        <small
          class="text-red-base block mt-2"
          v-for="(error, errorIndex) of validation.widget_term_condition.$errors"
          :key="errorIndex">
          Terms and Condition Link is required
        </small>
        
        <div class="mt-4 p-4 rounded-lg border border-grey-01">
          <div 
            class="flex items-start justify-between cursor-pointer" 
            @click="toggleAccordion"
            :disabled="loading.update"
          >
            <p class="font-semibold">Customize text for Terms & Conditions and Submit Button</p>
            <i v-loading="loading.all" class="material-icons transform transition-transform duration-300" :class="isOpen ? 'rotate-180' : ''">
              expand_more
            </i>
          </div>

          <div v-if="!contentMeasured && isOpen" class="hidden" ref="hiddenContent">
            <div class="space-y-4">
              <div>
                <label>Terms & Condition Placeholder</label>
                <TextEditor :options="ckeditor" v-model="form.widget_terms_custom_text" :toolbarItems="toolbarItems" />
              </div>

              <ul>
                <li class="flex items-center gap-2" v-for="shortcode in form.widget_terms_custom_text_shortcode" :key="shortcode.code">{{ shortcode.name }} 
                  <span>{{ shortcode.code }}</span>
                  <i class="material-icons w-5 text-teal-04 cursor-pointer">
                    content_copy_outlined
                  </i>
                </li>
              </ul>
              <Input 
                label="Submit Button Placeholder"
                class="w-full mt-8"
                v-model="form.widget_submit_custom_text"
              />
            </div>
          </div>
          <div 
            ref="accordionContent"
            class="overflow-hidden transition-all duration-300 ease-in-out"
            :style="accordionStyle"
          >
            <div class="space-y-4">
              <div>
                <label>Terms & Condition Placeholder</label>
                <TextEditor :onBlur="handleSave" :options="ckeditor" v-model="form.widget_terms_custom_text" :toolbarItems="toolbarItems" />
                <small class="text-red-base block mt-2" v-for="(error, errorIndex) of validation.widget_terms_custom_text.$errors" :key="errorIndex">{{ error.$message }}. </small>
              </div>
              <ul>
                <li class="flex items-center gap-2" v-for="shortcode in form.widget_terms_custom_text_shortcode" :key="shortcode.code">{{ shortcode.name }} 
                  <span>{{ shortcode.code }}</span>
                  <i @click="copyText(shortcode.code)" class="material-icons w-5 text-teal-04 cursor-pointer">
                      content_copy_outlined
                  </i>
                </li>
              </ul>
              <Input 
                label="Submit Button Placeholder"
                class="w-full mt-8"
                v-model="form.widget_submit_custom_text"
                @focus-out="handleSave"
                :disabled="loading.update"
                :loading="loading.all"
                :view="validation.widget_submit_custom_text.$invalid ? 'danger' : 'default'"
              />
            </div>
            <small class="text-red-base block mt-2" v-for="(error, errorIndex) of validation.widget_submit_custom_text.$errors" :key="errorIndex">Submit Button Placeholder is required </small>
          </div>
        </div>
      </form>
    </template>
  </Fieldset>
</template>

<script setup>
import { ref, reactive, computed, watch } from 'vue';
import Fieldset from 'bbUi/components/fieldset/fieldset.vue';
import Input from 'bbUi/components/input/input.vue';
import TextEditor from 'components/TextEditor.vue';
import { Editor as ClassicEditor } from '@erdeprsty/ckeditor5-custom-build';
import { ElMessage } from 'element-plus';
import useVuelidate from '@vuelidate/core';
import { required } from '@/validations/index'

const props = defineProps({
  modelValue: {
    type: Object,
    default: () => ({})
  },
  loading: {
    type: Object,
    default: () => ({})
  }
});

const emit = defineEmits(['update:modelValue']);

const ckeditor = reactive({
  editor: ClassicEditor,
  config: {
    htmlSupport: {
      allow: [
        {
          styles: true,
          classes: true,
        }
      ]
    }
  }
});

const toolbarItems = [
  'undo',
  'redo',
  'link',
  'horizontalLine',
  'specialCharacters',
  'heading',
  '|',
  'bold',
  'italic',
  'underline',
  'strikethrough',
  {
    label: 'Basic styles',
    icon: 'text',
    items: [
      'fontSize',
      'fontFamily',
      'fontColor',
      'fontBackgroundColor',
      'highlight',
      'superscript',
      'subscript',
      'code',
      '|',
      'textPartLanguage',
      '|'
    ]
  },
  'removeFormat',
  '|',
  'alignment',
  '|',
  'bulletedList',
  'numberedList',
  '|',
  'outdent',
  'indent'
];

const isOpen = ref(false);
const accordionContent = ref(null);
const hiddenContent = ref(null);
const contentHeight = ref(0);
const contentMeasured = ref(false);

const data = computed({
  get: () => props.modelValue,
  set: (value) => emit('update:modelValue', value)
});

const form = reactive({
  widget_term_condition: props.modelValue?.widget_term_condition || '',
  widget_terms_custom_text: props.modelValue?.widget_terms_custom_text || '',
  widget_terms_custom_text_shortcode: props.modelValue?.widget_terms_custom_text_shortcode || [],
  widget_submit_custom_text: props.modelValue?.widget_submit_custom_text || ''
});

const accordionStyle = computed(() => {
  if (!contentMeasured.value && isOpen.value) {
    return { 
      maxHeight: '2000px',
      opacity: 1,
      marginTop: '1rem'
    };
  }
  return {
    maxHeight: isOpen.value ? `${contentHeight.value}px` : '0px',
    opacity: isOpen.value ? 1 : 0,
    marginTop: isOpen.value ? '1rem' : '0'
  };
});

const copyText = (text) => {
  navigator.clipboard.writeText(text).then(() => {
    ElMessage({
      type: 'success',
      message: `${text} copied to clipboard`
    });
  });
};

const toggleAccordion = () => {
  isOpen.value = !isOpen.value;
};

const rules = {
  widget_term_condition: {
    required
  },
  widget_terms_custom_text: {
    required
  },
  widget_submit_custom_text: {
    required
  }
};

const validation = useVuelidate(rules, form, {
  $lazy: true
});

const validate = async () => {
  await validation.value.$validate();
  return !validation.value.$invalid;
};

const handleSave = async () => {
  const isValid = await validate();
  if (!isValid) {
    return;
  }
  
  Object.assign(data.value, form);
  emit('update:modelValue', data.value);
};


watch(
  () => props.modelValue,
  (newVal) => {
    if (newVal) {
      form.widget_term_condition = newVal.widget_term_condition || form.widget_term_condition;
      form.widget_terms_custom_text = newVal.widget_terms_custom_text || form.widget_terms_custom_text;
      form.widget_submit_custom_text = newVal.widget_submit_custom_text || form.widget_submit_custom_text;
    }
  },
  { immediate: true, deep: true }
);
</script>

<style scoped>
.ck_editor__editable {
  min-height: 150px !important;
  height: 150px !important;
}
</style>