<template>
  <Fieldset class="mb-6 rounded-xl">
    <template #label>{{ title }}</template>
    <template #default>
      <Select
        ref="elSelectPurchasingMethod"
        :modelValue="data.purchasing_method"
        @update:modelValue="handleUpdatePurchasingMethod"
        :items="purchases"
        placeholder="Please select method"
        max-height="200px"
        name="name"
        by="value"
        no-border
        block>
        <template #item="{ item }">
          {{ item.name }}
<!--          <div-->
<!--            v-if="item.value == 1 && !proplan"-->
<!--            class="inline-block py-1 px-4 ml-2 bg-green-04 text-white rounded-full"-->
<!--          >-->
<!--            <p>Pro</p>-->
<!--          </div>-->
        </template>
      </Select>
      <Fieldset class="mt-4 rounded-md">
        <template #label>Confirmation Message</template>
        <template #default>
          <WidgetConfirmationMessage
            v-model="dataModel"
            :staticData="staticData"
            :loading="loading"
            @update:message="handleSaveMessage"
          />
        </template>
      </Fieldset>
      <div class="mt-6 mb-2 px-4 py-4 space-y-4 border border-grey-03 border-opacity-50 rounded-md">
        <div class="flex items-center justify-between">
          <Checkbox
            v-model="data.purchasing_method_custom"
            type="toggle"
            name="custom"
            :value="1"
            :value-off="0">
            Add Custom
          </Checkbox>
          <span
            v-if="data.purchasing_method_custom"
            class="cursor-pointer"
            @click="showSetting = !showSetting">
            <i
              :class="[
                'material-icons transform',
                'transition-all ease-in-out',
                'duration-300',
                showSetting && 'rotate-180'
              ]">
              expand_more
            </i>
          </span>
        </div>
        <Transition name="slide-fade">
          <Button
            v-if="data.purchasing_method_custom && showSetting"
            color="teal"
            dense
            block
            @click="handleShowCustom">
            <div class="flex justify-center gap-x-2 items-center">
              <SVGLoader
                name="/menu-icon/setting-fill"
                id="buyback-icon"
                class="w-5 h-5"
              />
              Settings
            </div>
          </Button>
        </Transition>
      </div>
    </template>
  </Fieldset>

  <Modal
    v-model="customModal"
    title="Custom Purchasing Method"
    size="sm"
    @close="handleCloseModal">
    <form
      v-loading="loadingCustom"
      class="mt-6 space-y-4"
      @submit.prevent="handleSubmit">
      <div>
        <Input
          v-model="dataCustom.title"
          name="widget-purchase-custom-title"
          id="widgetPurchaseCustomTitle"
          label="Title"
          block
        />
        <small
          v-for="(error, errorIndex) of validation.title.$errors"
        :key="errorIndex"
          class="mt-2 text-red-base block">
          {{ error.$message }}.
        </small>
        <small
          v-if="!validation.title.$errors.length"
          class="text-grey-02">
          Maximal 32 characters
        </small>
      </div>
      <div>
        <Input
          v-model="dataCustom.description"
          name="widget-purchase-custom-description"
          id="widgetPurchaseCustomDescription"
          label="Description"
          block
        />
        <small
          v-for="(error, errorIndex) of validation.description.$errors"
          :key="errorIndex"
          class="mt-2 text-red-base block">
          {{ error.$message }}.
        </small>
        <small
          v-if="!validation.description.$errors.length"
          class="text-grey-02">
          Maximal 80 characters
        </small>
      </div>
      <div>
        <Input
          v-model="dataCustom.hyperlink"
          name="widget-purchase-custom-hyperlink"
          id="widgetPurchaseCustomHyperlink"
          label="Hyperlink"
          block
        />
        <small
          v-for="(error, errorIndex) of validation.hyperlink.$errors"
          :key="errorIndex"
          class="mt-2 text-red-base block">
          {{ error.$message }}.
        </small>
      </div>
      <Fieldset class="rounded-md">
        <template #label>
          Upload Icon
        </template>
        <template #default>
          <div class="max-w-md">
            <InputFile preview :file="dataCustom.icon" :action="false" @input="handleFile">
              <template #description>
                <small
                  v-for="(error, errorIndex) of validation.icon.$errors"
                  :key="errorIndex"
                  class="mt-2 text-red-base block">
                  {{ error.$message }}.
                </small>
                <small v-if="!validation.icon.$errors">
                  <b>Suggested</b>: 96x96px ;
                  <b>File</b>: PNG, JPG, JPEG, WEBP ;
                  <b>Max</b> 250KB
                </small>
              </template>
            </InputFile>
          </div>
        </template>
      </Fieldset>
    </form>
    <template #footer>
      <div class="flex items-center justify-end">
        <Button color="teal" @click="handleSubmit">
          Save Changes
        </Button>
      </div>
    </template>
  </Modal>
</template>

<script>
import { defineComponent, ref, computed, watch } from 'vue'
import { useVuelidate } from '@vuelidate/core'
import { required, maxLength, url, fileSize } from '@/validations/index'
import { useWidgetCustomButton } from 'composable/setting'
import { usePaywall } from 'composable/paywall'

import CollapsibleCard from 'components/CollapsibleCard.vue'
import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Checkbox from 'bbUi/components/checkbox/checkbox.vue'
import Button from 'bbUi/components/button/button.vue'
import PopoverDark from 'components/PopoverDark.vue'
import Modal from 'bbUi/components/modal/modal.vue'
import Input from 'bbUi/components/input/input.vue'
import SVGLoader from 'components/SVGLoader.vue'
import InputFile from 'components/InputFile.vue'
import Select from 'components/Select.vue'

import WidgetConfirmationMessage from 'components/Widget/Controller/ConfirmationMessage/index.vue'

export default defineComponent({
  name: 'WidgetPurchasing',
  components: {
    WidgetConfirmationMessage,
    CollapsibleCard,
    PopoverDark,
    InputFile,
    SVGLoader,
    Fieldset,
    Checkbox,
    Button,
    Select,
    Modal,
    Input
  },
  props: {
    modelValue: {
      type: Object,
      default: () => ({})
    },
    loading: {
      type: Object,
      default: () => ({})
    },
    staticData: {
      type: Object,
      default: undefined
    },
    title: {
      type: String,
      default: 'Purchasing Method'
    },
    proplan: {
      type: Boolean,
      default: false
    },
    whiteLabel: {
      type: Boolean,
      default: false
    }
  },
  emits: [
    'update:modelValue',
    'update:customButton',
    'update:message'
  ],
  setup(props, { emit }) {
    const {
      data: dataCustom,
      show: handleShow,
      update: handleUpdate,
      loading: loadingCustom
    } = useWidgetCustomButton()
    const { open: openPaywall } = usePaywall()
    
    const rulesForm = {
      title: { required, maxLength: maxLength(32) },
      description: { required, maxLength: maxLength(80) },
      hyperlink: { required, url },
      icon: { required, fileSize }
    }
    const customModal = ref(false)
    const showSetting = ref(false)
    dataCustom.value = {
      title: null,
      description: null,
      hyperlink: null,
      icon: null
    }
    const data = ref({
      purchasing_method: '1',
      purchasing_method_custom: true
    })
    const elSelectPurchasingMethod = ref(null)
    const dataModel = computed({
      get: () => props.modelValue,
      set: (value) => emit('update:modelValue', value)
    })
    const purchases = ref([
      { name: 'Both (Mail-In & In-Store)', value: '1' },
      { name: 'Mail-In', value: '2' },
      { name: 'In-Store', value: '3' }
    ])
    const validation = useVuelidate(rulesForm, dataCustom, {
      $lazy: true
    })

    const handleSaveMessage = (val) => emit('update:message', val)
    const handleShowCustom = async () => {
      validation.value.$reset()
      customModal.value = true
      await handleShow()
    }
    const handleCloseModal = () => {
      dataCustom.value = {
        title: null,
        description: null,
        hyperlink: null,
        icon: null
      }
    }
    const handleFile = (e) => {
      dataCustom.value.icon = e
    }
    const handleSubmit = async () => {
      await validation.value.$validate()
      if (!validation.value.$invalid) {
        await handleUpdate(async () => {
          await handleShow()
          data.value.custom = true
          customModal.value = false
        })
        validation.value.$reset()
        return emit('update:customButton')
      }
    }
    const handleUpdatePurchasingMethod = async (newValue) => {
      // const isBothPurchasingMethod = newValue == 1
      // const isNotProPlan = !props.proplan
      // if(isBothPurchasingMethod) {
      //   if(isNotProPlan){
      //     openPaywall({
      //       title: 'Capture more Customers',
      //       description: 'Give your customers more ways to complete a buyback, thereby increasing conversion rates. Allow them to choose the most convenient option for them: selling online or in-store.',
      //       image_url: '/images/paywall/paywall-widget-purchasing-method.svg'
      //     })
      //     if(elSelectPurchasingMethod.value) elSelectPurchasingMethod.value.resetValue()
      //     return
      //   }
      // }
      data.value.purchasing_method = newValue
    }

    watch(
      () => dataModel.value,
      (value) => {
        data.value.purchasing_method = value.purchasing_method.value
        data.value.purchasing_method_custom = value.purchasing_method.custom
        showSetting.value = data.value.purchasing_method_custom
      },
      { immediate: true, deep: true }
    )

    watch(() => ({
      method: data.value.purchasing_method,
      custom: data.value.purchasing_method_custom
    }), (val) => {
      if (val.custom) showSetting.value = true
      dataModel.value.purchasing_method.value = val.method
      dataModel.value.purchasing_method.custom = val.custom
    })

    return {
      elSelectPurchasingMethod,
      data,
      dataModel,
      dataCustom,
      purchases,
      validation,
      customModal,
      showSetting,
      loadingCustom,
      handleShow,
      handleFile,
      handleUpdate,
      handleSubmit,
      handleShowCustom,
      handleCloseModal,
      handleSaveMessage,
      handleUpdatePurchasingMethod
    }
  }
})
</script>
