<template>
  <draggable
    v-model="myList"
    v-bind="dragOptions"
    tag="ul"
    :group="{
      name: `deviceD${index}`,
      put: true,
      pull: false
    }"
    handle=".drag-handle"
    class="drag-item-container"
    @start="isDragged = true"
    @end="isDragged = false"
    @change="changeValue">
    <transition-group
      type="transition"
      :name="!isDragged ? 'drag-flip-list' : null">
      <li
        v-for="(el, n) in myList"
        :key="n"
        class="drag-item-group border-t border-grey-02 relative">
        <div class="drag-item-group-title">
          <div class="hover:bg-teal-01">
            <div class="drag-item-group-title-info">
              <div class="mx-4 flex items-center gap-x-3 py-3">
                <button
                  :class="[!isShowChild ? 'cursor-move drag-handle' : 'cursor-not-allowed opacity-50']"
                  class="p-0">
                  <i class="material-icons">drag_indicator</i>
                </button>
                <div class="flex items-center gap-x-2">
                  <button
                    v-if="el.list"
                    :class="['drag-icon', el.show ? 'rotate' : '']"
                    @click="showChild(n)">
                    <i class="material-icons">arrow_right</i>
                  </button>
                  <p>{{ el.name }}</p>
                </div>
              </div>
            </div>
          </div>  
        </div>
        <transition name="drag-collapse">
          <div v-if="el.show && el.list">
            <DragList
              ref="listChild"
              v-model="el.list"
              :my-key="el.key"
              class="drag-item-sub"
              :lastLine="false"
              child
              @change-child="changeChild"
            />
          </div>
        </transition>
      </li>
    </transition-group>
    <hr
      v-if="isLastLine"
      slot:name="footer"
      class="my-0 border-grey-02"
    />
  </draggable>
</template>

<script>
import { VueDraggableNext } from 'vue-draggable-next'
import { defineComponent, ref, watch, computed } from 'vue'

export default defineComponent({
  name: 'DragList',
  components: {
    draggable: VueDraggableNext,
  },
  props: {
    myKey: {
      type: [Number, String],
      default: undefined
    },
    modelValue: {
      type: Array,
      default: () => []
    },
    child: {
      type: Boolean,
      default: false
    },
    lastLine: {
      type: Boolean,
      default: true
    }
  },
  emits: ['update:modelValue', 'change', 'opened', 'change-child'],
  setup(props, { emit }) {
    const listChild = ref(undefined)
    const index = computed(() => props.myKey ? props.key : '')
    const disabled = ref(false)
    const isDragged = ref(false)
    const isLastLine = computed(() => props.lastLine)
    const myList = ref([])
    const dragOptions = computed(() => ({
      animation: 300,
      disabled: isShowChild.value,
      ghostClass: 'drag-ghost'
    }))
    const isChild = computed(() => props.child)
    const realValue = computed(() => myList.value)
    const isShowChild = computed(() => {
      const showed = [...myList.value].filter(item => item.show)
      if (showed.length) return true
      return false
    })
    const showChild = (e) => {
      myList.value[e].show = !myList.value[e].show
      if (myList.value[e].show) emit('opened', myList.value[e])
      return myList.value[e]
    }
    const changeValue = () => {
      emit('change', myList.value)
      if (props.child) emit('change-child', { key: props.myKey, value: myList.value })
      emit('update:modelValue', myList.value)
    }

    const changeChild = (value) => {
      emit('change-child', {
        key: props.myKey,
        ...value
      })
    }

    watch(
      () => props.modelValue,
      () => myList.value = props.modelValue,
      { immediate: true }
    )

    return {
      index,
      myList,
      showChild,
      realValue,
      isChild,
      isDragged,
      disabled,
      dragOptions,
      isShowChild,
      isLastLine,
      changeValue,
      changeChild,
      listChild
    }
  }
})
</script>
<style lang="scss" scoped>
.drag-icon {
  transform: rotate(0);
  transition: transform .3s ease-in-out;
  &.rotate {
    transform: rotate(90deg);
  }
}
.drag-item-sub {
  .drag-item-group {
    .drag-item-group-title {
      .drag-item-group-title-info {
        padding-left: 1.5rem;
      }
    }
    .drag-item-sub {
      .drag-item-group {
        padding-left: 1.5rem;
      }
    }
  }
}
.drag-ghost {
  opacity: .5;
  background-color: #4ef6e551;
}
.drag-collapse-enter-active {
  animation: collapse reverse 500ms ease;
}
.drag-collapse-leave-active {
  animation: collapse 500ms ease;
}
.drag-flip-list-move {
  transition: transform 0.5s;
}
.list-test-enter-active,
.list-test-leave-active {
  transition: all 0.5s ease;
}
.list-test-enter-from,
.list-test-leave-to {
  opacity: 0;
  transform: translateX(30px);
}
@keyframes collapse {
  from {
    max-height: 200px;
    opacity: 1;
  }
  to {
    opacity: 0;
    max-height: 0px;
  }
}
</style>
