<template>
	<div
		class="relative">
		<div
			:class="[
				'cursor-pointer rounded-md',
				'bg-white px-6 py-2 text-base',
				'border transition-all ease-in-out duration-300',
				showPanel ? 'border-teal-04' : 'border-teal-01'
			]"
			@click.stop="handleShowPanel">
			<div class="flex items-center justify-between gap-x-4">
				<div class="flex items-center gap-x-4">
					<div
						class="rounded-md h-6 w-6 border border-grey-01"
						:style="{ backgroundColor: color }"
					/>
					<p>{{ color }}</p>
				</div>
				<span
					:class="[
						'material-icons',
						'text-grey-03 transform',
						'transition-all linear duration-300',
						showPanel ? '-rotate-180' : 'rotate-0'
					]">
					arrow_drop_down
				</span>
			</div>
		</div>
		<transition name="fade">
			<div
				v-if="showPanel"
				:class="[
					'absolute bg-white shadow',
					'px-4 py-4 mt-2 z-10 rounded-md',
					`color-picker-${placement}`
				]"
				:style="{
					...(
						Number(widthPanel) &&
						!['top', 'bottom'].includes(placement) &&
						{ width: `${widthPanel}px` }
					)
				}"
				v-outside="handleClosePanel">
				<ColorPickerPanel
					v-model="color"
					v-bind="vBind"
					@color-change="handleChangeColors"
				/>
			</div>
		</transition>
	</div>
</template>
<script>
import { computed, defineComponent, ref, watch } from 'vue';
import ColorPickerPanel from './Panel.vue'

export default defineComponent({
	name: 'ColorPicker',
	components: {
		ColorPickerPanel
	},
	props: {
		modelValue: {
			type: String,
			default: '#fffffff',
		},
		id: {
			type: String,
			default: 'panel-color-picker',
		},
		visibleFormats: {
			type: Array,
			default: () => ['hex', 'hsl', 'hwb', 'rgb'],
		},
		defaultFormat: {
			type: String,
			default: 'rgb',// 'hsl',
		},
		alphaChannel: {
			type: String,
			default: 'hide', // 'show' | 'hide',
		},
		placement: {
			type: String,
			default: 'bottom',
			// ['bottom', 'bottom-start', 'bottom-end']
			// ['top', 'top-start', 'top-end']
		},
		widthPanel: {
			type: [String, Number],
			default: undefined
		}
	},
	emits: ['color-change', 'color-copy', 'update:modelValue'],
	setup(props, ctx) {
		const color = ref(props.modelValue)
		const colors = ref({})
		const vBind = computed(() => ({
			id: props.id,
			visibleFormats: props.visibleFormats,
			defaultFormat: props.defaultFormat,
			alphaChannel: props.alphaChannel,
		}))
		const showPanel = ref(false)

		const handleShowPanel = () => {
			showPanel.value = !showPanel.value
		}
		const handleClosePanel = () => {
			showPanel.value = false
		}
		const handleChangeColors = (e) => {
			colors.value = e
			ctx.emit('color-change', colors.value)
		}

		// Watcher to emit `update:modelValue` only when `showPanel` closes and color has changed
		watch(showPanel, (newVal) => {
			if (!newVal && color.value !== props.modelValue) {
				ctx.emit('update:modelValue', color.value)
			}
		})

		return {
			color,
			vBind,
			colors,
			showPanel,
			handleShowPanel,
			handleClosePanel,
			handleChangeColors,
		}
	}
})
</script>
<style lang="scss" scoped>
.color-picker-top,
.color-picker-top-start,
.color-picker-top-end {
	@apply bottom-full left-0 right-0;
}

.color-picker-bottom,
.color-picker-bottom-start,
.color-picker-bottom-end {
	@apply top-full left-0 right-0;
}
.color-picker-width {
	@apply w-auto;
}

@media screen and (min-width: 1280px) {
	.color-picker-top-start,
	.color-picker-bottom-start {
		right: unset;
	}
	.color-picker-top-end,
	.color-picker-bottom-end {
		left: unset;
	}

	.color-picker-width {
		width: unset;
	}
}
</style>