<template>
  <Fieldset class="mb-6 rounded-xl">
    <template #label>Starting Point</template>
    <template #default>
      <div>
        <div class="mb-2 font-bold">Widget Starting Point</div>
        <div>
          <Radio
            v-model="starting.is_widget_enable_brand_step"
            class="font-normal"
            value="1">
            Brand
          </Radio>
        </div>
        <div>
          <Radio
            v-model="starting.is_widget_enable_brand_step"
            class="font-normal"
            value="2">
            Category
          </Radio>
        </div>
        <div>
          <Radio
            v-model="starting.is_widget_enable_brand_step"
            class="font-normal"
            value="0">
            Product
          </Radio>
        </div>
        <div class="space-y-6">
          <Radio
            v-model="starting.is_widget_enable_brand_step"
            class="font-normal"
            value="3"
            @click.prevent="handleWidgetSearchbar">
            <div class="flex items-center gap-x-3">
              Search Bar
              <Tooltip placement="top">
                <template #content>
                  <div class="w-72 text-justify text-sm">
                    This setting will only display a search bar and will populate products as the user types.
                  </div>
                </template>
                <i class="text-teal-04 material-icons cursor-pointer">
                  info
                </i>
              </Tooltip>
              <div v-if="!proplan" class="block py-1 px-4 bg-green-04 text-white rounded-full">
                <p>Pro</p>
              </div>
            </div>
          </Radio>
          <form
            v-if="starting.is_widget_enable_brand_step == '3'"
            class="space-y-4"
            autocomplete="off"
            @submit.prevent="handleSearchChangeText">
            <Input
              v-model="searchText"
              label="Search Bar Text"
              placeholder="Search"
              block
            />
            <!-- <div class="flex justify-end">
              <Button
                :loading="loading.update"
                color="teal"
                type="submit"
                dense
                @click="handleSearchChangeText">
                Save & Apply
              </Button>
            </div> -->
          </form>
        </div>
      </div>
    </template>
  </Fieldset>
</template>

<script>
import { computed, defineComponent, ref, watch } from 'vue'
import { usePaywall } from 'composable/paywall'

import Fieldset from 'bbUi/components/fieldset/fieldset.vue'
import Button from 'bbUi/components/button/button.vue'
import Radio from 'bbUi/components/radio/radio.vue'
import Input from 'bbUi/components/input/input.vue'

export default defineComponent({
  name: 'WidgetStartingPoint',
  components: {
    Fieldset,
    Button,
    Radio,
    Input
  },
  props: {
    modelValue: {
      type: Object,
      default: () => ({})
    },
    loading: {
      type: Object,
      default: () => ({})
    },
    proplan: {
      type: Boolean,
      default: false
    },
    whiteLabel: {
      type: Boolean,
      default: false
    }
  },
  emits: ['update:modelValue'],
  setup(props, { emit }) {
    const { open: openPaywall } = usePaywall()
    const starting = computed({
      get: () => props.modelValue,
      set: (value) => emit('update:modelValue', value)
    })
    const searchText = ref(null)

    const handleWidgetSearchbar = () => {
      if(!props.proplan) {
        openPaywall({
          title: 'Search Bar',
          description: 'Start the buyback widget at a search bar. Using machine learning and advanced algorithms we perfected the search for buybacks. Allow customers to find and complete buybacks up to 25% faster than clicking.',
          image_url: '/images/paywall/paywall-widget-searchbar.svg'
        })
        return
      }
      starting.value.is_widget_enable_brand_step = '3'
    }
    const handleSearchChangeText = () => {
      starting.value.search_bar_text = searchText.value
    }

    watch(() => starting.value.search_bar_text, (val) => {
      searchText.value = val
    }, {
      immediate: true
    })

    return {
      starting,
      searchText,
      handleWidgetSearchbar,
      handleSearchChangeText
    }
  }
})
</script>