<template>
  <div v-outside="closeSheet" class="floating-sheets" :style="referenceStyles">
    <div
      ref="refReference"
      class="floating-sheets__reference"
      :style="referenceStyles"
    >
      <slot
        v-bind="{ openSheet, closeSheet, toggleSheet }"
        name="reference"
      ></slot>
    </div>
    <transition name="fade">
      <div v-show="isCollapse" ref="refSheet" class="floating-sheets__sheet">
        <slot v-bind="{ openSheet, closeSheet, toggleSheet }"></slot>
      </div>
    </transition>
  </div>
</template>
<script>
import {
  defineComponent,
  ref,
  computed,
  onMounted,
  onUnmounted,
  nextTick
} from 'vue'
import debounce from 'utils/debounce'

export default defineComponent({
  props: {
    block: {
      type: Boolean,
      default: false
    },
    alignment: {
      type: String,
      default: 'bottom-left',
      validator: (alignment) => {
        const validAlignments = [
          'top-left',
          'top-center',
          'top-right',
          'bottom-left',
          'bottom-center',
          'bottom-right'
        ]
        if (alignment && typeof alignment === 'string') {
          return validAlignments.includes(alignment)
        }
        return false
      }
    },
    maxWidth: {
      type: [String, Number],
      default: ''
    },
    minWidth: {
      type: [String, Number],
      default: ''
    },
    zIndex: {
      type: [String, Number],
      default: ''
    },
    coverWidth: {
      type: Boolean,
      default: false
    }
  },
  setup(props) {
    const refSheet = ref(null)
    const refReference = ref(null)
    const isCollapse = ref(false)

    const referenceStyles = computed(() => ({
      width: props.block ? '100%' : 'max-content'
    }))
    const sheetAlignment = computed(() => {
      if (props.alignment) {
        const [verticalAlignment, horizontalAlignment] =
          props.alignment.split('-')
        return {
          vertically: verticalAlignment,
          horizontally: horizontalAlignment
        }
      }
      return {
        vertically: 'bottom',
        horizontally: 'center'
      }
    })

    const calculateSheetPosition = () => {
      if (refSheet.value && refReference.value) {
        const {
          top: referenceTop,
          bottom: referenceBottom,
          height: referenceHeight,
          left: referenceLeft,
          right: referenceRight,
          width: referenceWidth
        } = refReference.value.getBoundingClientRect()

        if (sheetAlignment.value.vertically === 'bottom') {
          refSheet.value.style.top = `${referenceTop + referenceHeight}px`
          refSheet.value.style.bottom = 'unset'
        } else {
          refSheet.value.style.bottom = `${
            window.innerHeight - referenceBottom + referenceHeight
          }px`
          refSheet.value.style.top = 'unset'
        }

        if (sheetAlignment.value.horizontally === 'left') {
          refSheet.value.style.left = `${referenceLeft}px`
          refSheet.value.style.right = 'unset'
        } else if (sheetAlignment.value.horizontally === 'right') {
          refSheet.value.style.right = `${window.innerWidth - referenceRight}px`
          refSheet.value.style.left = 'unset'
        } else {
          refSheet.value.style.right = 'unset'
          nextTick(() => {
            refSheet.value.style.left = `${
              referenceLeft -
              refSheet.value.getBoundingClientRect().width / 2 +
              referenceWidth / 2
            }px`
          })
        }

        if (props.zIndex && !isNaN(parseInt(props.zIndex)))
          refSheet.value.style.zIndex = `${props.zIndex}`
        else refSheet.value.style.zIndex = '99'

        if (props.maxWidth && !isNaN(parseInt(props.maxWidth))) {
          refSheet.value.style.maxWidth = `${parseInt(props.maxWidth)}px`
        } else {
          refSheet.value.style.maxWidth = `100%`
        }

        if (props.minWidth && !isNaN(parseInt(props.minWidth)))
          refSheet.value.style.minWidth = `${parseInt(props.minWidth)}px`
        if(props.coverWidth){
          refSheet.value.style.minWidth = `${referenceWidth}px`
        }
      }
    }
    const openSheet = () => {
      calculateSheetPosition()
      isCollapse.value = true
    }
    const closeSheet = () => (isCollapse.value = false)
    const toggleSheet = () => {
      isCollapse.value = !isCollapse.value
      if (isCollapse.value) calculateSheetPosition()
    }

    const onScroll = () => calculateSheetPosition()
    onMounted(() =>
      window.addEventListener('scroll', debounce(onScroll, 100), true)
    )
    onUnmounted(() => window.removeEventListener('scroll', onScroll))

    return {
      refSheet,
      refReference,
      isCollapse,
      referenceStyles,
      openSheet,
      closeSheet,
      toggleSheet
    }
  }
})
</script>
<style lang="scss">
.floating-sheets {
  .floating-sheets__sheet {
    @apply transition-all duration-300 fixed bg-white border border-grey-01 mt-2 rounded-lg right-0;
    .input__container {
      .input__wrapper {
        input {
          border-top-right-radius: 0.5rem !important;
          border-bottom-right-radius: 0.5rem !important;
        }
      }
    }
  }
}
</style>
