<template>
	<div
		ref="colorPicker"
		class="panel-color-picker"
	>
		<div
			ref="colorSpace"
			class="panel-color-picker-color-space"
			@mousedown="startMovingThumbWithMouse"
			@touchstart="startMovingThumbWithTouch"
		>
			<div
				ref="thumb"
				class="panel-color-picker-color-space-thumb"
				tabindex="0"
				aria-label="Color space thumb"
				@keydown="moveThumbWithArrows"
			/>
		</div>

		<div class="panel-color-picker-range-input-group">
			<input
				:id="`${id}-hue-slider`"
				type="range"
				min="0"
				max="360"
				step="1"
				v-model="colors.hsv.h"
				class="h-3 appearance-none cursor-pointer slider rounded-full"
				:style="{ '--thumbColor': `hsl(${colors.hsl.h} 100% 50%)` }"
				@keydown.passive="changeInputValue"
				@input="handleSliderInput($event, 'h')"
			/>

			<label
				v-if="alphaChannel === 'show'"
				class="panel-color-picker-range-input-label panel-color-picker-range-input-label--alpha"
				:for="`${id}-alpha-slider`"
			>
				<span class="panel-color-picker-range-input-label-text panel-color-picker-range-input-label-text--alpha">
					<slot name="alpha-range-input-label">Alpha</slot>
				</span>

				<input
					:id="`${id}-alpha-slider`"
					class="panel-color-picker-range-input panel-color-picker-range-input--alpha"
					:value="colors.hsv.a"
					type="range"
					min="0"
					max="1"
					step="0.01"
					@keydown.passive="changeInputValue"
					@input="handleSliderInput($event, 'a')"
				>
			</label>
		</div>

		<button
			v-if="false"
			class="panel-color-picker-copy-button"
			type="button"
			@click="copyColor"
		>
			<slot name="copy-button">
				<span class="panel-color-picker-visually-hidden">Copy color</span>

				<svg
					class="panel-color-picker-icon"
					xmlns="http://www.w3.org/2000/svg"
					aria-hidden="true"
					width="24"
					height="24"
					viewBox="0 0 32 32"
				>
					<path
						d="M25.313 28v-18.688h-14.625v18.688h14.625zM25.313 6.688c1.438 0 2.688 1.188 2.688 2.625v18.688c0 1.438-1.25 2.688-2.688 2.688h-14.625c-1.438 0-2.688-1.25-2.688-2.688v-18.688c0-1.438 1.25-2.625 2.688-2.625h14.625zM21.313 1.313v2.688h-16v18.688h-2.625v-18.688c0-1.438 1.188-2.688 2.625-2.688h16z"
						fill="currentColor"
					/>
				</svg>
			</slot>
		</button>

		<div class="flex items-center justify-between gap-x-2">
			<div class="flex-1 flex items-center justify-between gap-x-2">
				<div>
					<label
						:for="`${id}-color-hex`"
						class="text-sm">
						Hex
					</label>
					<Input
						v-model="hexInputValue"
						:id="`${id}-color-hex`"
						type="text"
						block
						dense
						@input="updateHexColorValue"
					/>
				</div>
				<div class="grid grid-cols-3 gap-x-2">
					<div
						v-for="channel in visibleChannels"
						:id="`${id}-color-${activeFormat}-${channel}-label`"
						:key="`${id}-color-${activeFormat}-${channel}-label`">
						<label
							:for="`${id}-color-${activeFormat}-${channel}`"
							class="text-sm">
							{{ channel.toUpperCase() }}
						</label>
						<Input
							:id="`${id}-color-${activeFormat}-${channel}`"
							:value="getChannelAsCssValue(channel)"
							type="text"
							block
							dense
							@input="updateColorValue($event, channel)"
						/>
					</div>
				</div>
			</div>

			<!-- <button
				v-if="visibleFormats.length > 1"
				type="button"
				@click="switchFormat"
			>
				Siwtch
			</button> -->
		</div>
	</div>
</template>

<script>
import {
	computed,
	onBeforeUnmount,
	onMounted,
	reactive,
	ref,
	watch,
	defineComponent,
} from 'vue'

import { clamp } from 'utils/colors/clamp.js'
import { colorsAreValueEqual } from 'utils/colors/colorsAreValueEqual.js'
import { convert } from 'utils/colors/convert.js'
import { getCssValue } from 'utils/colors/cssValues.js'
import { formatAsCssColor } from 'utils/colors/formatAsCssColor.js'
import { isValidHexColor } from 'utils/colors/isValidHexColor.js'
import { parsePropsColor } from 'utils/colors/parsePropsColor.js'
import { getNewThumbPosition } from 'utils/colors/getNewThumbPosition.js'

import Input from 'bbUi/components/input/input.vue'

export default defineComponent({
  name: 'ColorPickerPanel',
	components: {
		Input
	},
	props: {
		modelValue: {
			type: String,
			default: '#ffffffff',
		},
		id: {
			type: String,
			default: 'panel-color-picker',
		},
		visibleFormats: {
			type: Array,
			default: () => ['hex', 'hsl', 'hwb', 'rgb'],
		},
		defaultFormat: {
			type: String,
			default: 'rgb',// 'hsl',
		},
		alphaChannel: {
			type: String,
			default: 'hide', // 'show' | 'hide',
		},
	},
	emits: ['color-change', 'color-copy', 'update:modelValue'],
	setup(props, { emit }) {
		const COLOR_FORMATS = ['hex', 'hsl', 'hsv', 'hwb', 'rgb'];

		const colorPicker = ref(null);
		const colorSpace = ref(null);
		const thumb = ref(null);
		let pointerOriginatedInColorSpace = false;

		const activeFormat = ref(props.visibleFormats.includes(props.defaultFormat) ? props.defaultFormat : props.visibleFormats[0]);

		const colors = reactive({
			hex: '#ffffffff',
			hsl: { h: 0, s: 0, l: 100, a: 1 },
			hsv: { h: 0, s: 0, v: 100, a: 1 },
			hwb: { h: 0, w: 100, b: 0, a: 1 },
			rgb: { r: 255, g: 255, b: 255, a: 1 },
		});

		const visibleChannels = computed(() => {
			const allChannels = Object.keys(colors[activeFormat.value]);
			return activeFormat.value !== 'hex' && props.alphaChannel === 'hide' ? allChannels.slice(0, 3) : allChannels;
		});

		const hexInputValue = computed(() => {
			return props.alphaChannel === 'hide' && [5, 9].includes(colors.hex.length)
				? colors.hex.substring(0, colors.hex.length - (colors.hex.length - 1) / 4)
				: colors.hex;
		});

		watch(() => props.modelValue, setColorFromProp);

		onMounted(() => {
			document.addEventListener('mousemove', moveThumbWithMouse, { passive: false });
			document.addEventListener('touchmove', moveThumbWithTouch, { passive: false });
			document.addEventListener('mouseup', stopMovingThumb);
			document.addEventListener('touchend', stopMovingThumb);
			setColorFromProp(props.modelValue);
		});

		onBeforeUnmount(() => {
			document.removeEventListener('mousemove', moveThumbWithMouse);
			document.removeEventListener('touchmove', moveThumbWithTouch);
			document.removeEventListener('mouseup', stopMovingThumb);
			document.removeEventListener('touchend', stopMovingThumb);
		});

		function switchFormat() {
			const activeFormatIndex = props.visibleFormats.findIndex(format => format === activeFormat.value);
			const newFormatIndex = (activeFormatIndex + 1) % props.visibleFormats.length;
			activeFormat.value = props.visibleFormats[newFormatIndex];
		}

		function startMovingThumbWithMouse(event) {
			pointerOriginatedInColorSpace = true;
			moveThumbWithMouse(event);
		}

		function startMovingThumbWithTouch(event) {
			pointerOriginatedInColorSpace = true;
			moveThumbWithTouch(event);
		}

		function stopMovingThumb() {
			pointerOriginatedInColorSpace = false;
		}

		function moveThumbWithMouse(event) {
			if (event.buttons !== 1 || !pointerOriginatedInColorSpace || !(colorSpace.value instanceof HTMLElement)) return;
			moveThumb(colorSpace.value, event.clientX, event.clientY);
		}

		function moveThumbWithTouch(event) {
			if (!pointerOriginatedInColorSpace || !(colorSpace.value instanceof HTMLElement)) return;
			event.preventDefault();
			const touchPoint = event.touches[0];
			moveThumb(colorSpace.value, touchPoint.clientX, touchPoint.clientY);
		}

		function moveThumb(colorSpace, clientX, clientY) {
			const newThumbPosition = getNewThumbPosition(colorSpace, clientX, clientY);
			const hsvColor = { ...colors.hsv, s: newThumbPosition.x, v: newThumbPosition.y };
			setColor('hsv', hsvColor);
		}

		function moveThumbWithArrows(event) {
			if (!['ArrowUp', 'ArrowRight', 'ArrowDown', 'ArrowLeft'].includes(event.key)) return;
			event.preventDefault();
			const direction = ['ArrowLeft', 'ArrowDown'].includes(event.key) ? -1 : 1;
			const channel = ['ArrowLeft', 'ArrowRight'].includes(event.key) ? 's' : 'v';
			const step = event.shiftKey ? 10 : 1;
			const newColorValue = colors.hsv[channel] + direction * step;
			const hsvColor = { ...colors.hsv, [channel]: clamp(newColorValue, 0, 100) };
			setColor('hsv', hsvColor);
		}

		function setColorFromProp(propsColor) {
			const result = parsePropsColor(propsColor);
			if (result !== null) {
				setColor(result.format, result.color);
			}
		}

		function handleSliderInput(event, channel) {
			const input = event.currentTarget;
			const hsvColor = { ...colors.hsv, [channel]: Number(input.value) };
			setColor('hsv', hsvColor);
		}

		function updateHexColorValue(event) {
			const input = event.target;
			if (isValidHexColor(input.value)) {
				setColor('hex', input.value);
			}
		}

		function updateColorValue(event, channel) {
			const input = event.target;
			const format = activeFormat.value;
			const color = { ...colors[format] };
			const cssValue = getCssValue(format, channel);
			const value = cssValue.from(input.value);

			if (Number.isNaN(value) || value === undefined) return;

			color[channel] = value;
			setColor(format, color);
		}

		function setColor(format, color) {
			let normalizedColor = color;
			if (props.alphaChannel === 'hide') {
				if (typeof color !== 'string') {
					color.a = 1;
					normalizedColor = color;
				} else if ([5, 9].includes(color.length)) {
					const alphaChannelLength = (color.length - 1) / 4;
					normalizedColor = color.substring(0, color.length - alphaChannelLength) + 'f'.repeat(alphaChannelLength);
				} else if ([4, 7].includes(color.length)) {
					normalizedColor = color + 'f'.repeat((color.length - 1) / 3);
				}
			}

			if (!colorsAreValueEqual(colors[format], normalizedColor)) {
				colors[format] = normalizedColor;

				for (const targetFormat of COLOR_FORMATS) {
					if (targetFormat !== format) {
						colors[targetFormat] = convert(format, targetFormat, normalizedColor);
					}
				}

				emit('color-change', getColorChangeDetail());
				emit('update:modelValue', hexInputValue.value)
			}

			if (colorPicker.value instanceof HTMLElement && colorSpace.value instanceof HTMLElement && thumb.value instanceof HTMLElement) {
				setCssProps(colorPicker.value, colorSpace.value, thumb.value);
			}
		}

		async function copyColor() {
			const activeColor = colors[activeFormat.value];
			const excludeAlphaChannel = props.alphaChannel === 'hide';
			const cssColor = formatAsCssColor({ color: activeColor, format: activeFormat.value }, excludeAlphaChannel);
			await window.navigator.clipboard.writeText(cssColor);
			emit('color-copy', getColorChangeDetail());
		}

		function getChannelAsCssValue(channel) {
			const format = activeFormat.value;
			const cssValue = getCssValue(format, channel);
			return cssValue.to(colors[format][channel]);
		}

		function setCssProps(colorPicker, colorSpace, thumb) {
			const opaqueColor = formatAsCssColor({ format: 'hsl', color: colors.hsl }, false);
			colorPicker.style.setProperty('--vacp-color', opaqueColor);
			colorSpace.style.position = 'relative';
			colorSpace.style.backgroundColor = `hsl(${colors.hsl.h} 100% 50%)`;
			colorSpace.style.backgroundImage = 'linear-gradient(to top, #000, transparent), linear-gradient(to right, #fff, transparent)';
			colorPicker.style.overflow = 'visible'
			thumb.style.left = `${colors.hsv.s}%`;
			thumb.style.bottom = `${colors.hsv.v}%`;
			thumb.style.position = 'absolute';
			thumb.style.backgroundColor = colors.hex;
		}

		function getColorChangeDetail() {
			return {
				color: colors[activeFormat.value],
				format: activeFormat.value,
			};
		}

		return {
			colorPicker,
			colorSpace,
			thumb,
			activeFormat,
			colors,
			visibleChannels,
			hexInputValue,
			switchFormat,
			startMovingThumbWithMouse,
			startMovingThumbWithTouch,
			stopMovingThumb,
			moveThumbWithMouse,
			moveThumbWithTouch,
			moveThumb,
			moveThumbWithArrows,
			setColorFromProp,
			handleSliderInput,
			updateHexColorValue,
			updateColorValue,
			copyColor,
			getChannelAsCssValue,
		};
	 },
});
</script>


<style lang="scss">

$spacing: 6px;

.panel-color-picker {
	@apply space-y-4 rounded-md;
}

.panel-color-picker-color-space {
	grid-column: 1 / -1;
	overflow: visible !important;
	height: 128px;
	widows: 100%;
	@apply rounded-md;
}

.panel-color-picker-color-space-thumb {
	--cp-thumb-size: calc(var(--cp-spacing, #{$spacing}) * 4);

	width: 20px;
	height: 20px;
	margin-left: calc(-1 * var(--cp-thumb-size) / 2);
	margin-bottom: calc(-1 * var(--cp-thumb-size) / 2);
	border: 5px solid #fff;
	border-radius: 50%;
	transform: rotate(0);
	@apply shadow;
}

$hue-gradient: linear-gradient(
	to right,
#f00 calc(100% *   0/360),
#ff0 calc(100% *  60/360),
#0f0 calc(100% * 120/360),
#0ff calc(100% * 180/360),
#00f calc(100% * 240/360),
#f0f calc(100% * 300/360),
#f00 calc(100% * 360/360)
);

.panel-color-picker-visually-hidden {
	position: absolute;
	overflow: hidden;
	clip: rect(0 0 0 0);
	width: 1px;
	height: 1px;
	margin: -1px;
	padding: 0;
	border: 0;
	white-space: nowrap;
}

/* Styles for the range input */
input[type='range'] {
  -webkit-appearance: none; /* Override default WebKit styles */
  appearance: none; /* Override default styles */
  width: 100%;
	background: $hue-gradient;
}

/* Style the slider track */
input[type='range']::-webkit-slider-runnable-track {
  height: 8px;
  border-radius: 5px;
}

/* Style the slider thumb */
input[type='range']::-webkit-slider-thumb {
  -webkit-appearance: none; /* Override default WebKit styles */
  appearance: none; /* Override default styles */
  width: 20px; /* Thumb width */
  height: 20px; /* Thumb height */
  border-radius: 50%; /* Make thumb circular */
  background-color: var(--thumbColor); /* Use computed color value */
  border: 5px solid #FFFFFF; /* Thumb border color */
  cursor: pointer; /* Pointer cursor on hover */
  margin-top: -6px; /* Center the thumb on the track */
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.5);
}

/* For Firefox */
input[type='range']::-moz-range-thumb {
  width: 20px; /* Thumb width */
  height: 20px; /* Thumb height */
  border-radius: 50%; /* Make thumb circular */
  background-color: var(--thumbColor); /* Use computed color value */
  border: 5px solid #FFFFFF; /* Thumb border color */
  cursor: pointer; /* Pointer cursor on hover */
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.5);
}

/* Style the track in Firefox */
input[type='range']::-moz-range-track {
  height: 8px;
  border-radius: 5px;
}
</style>
