<template>
  <div class="space-y-4">
    <div>
      <Select
        v-model="selected.by"
        :items="itemsBy"
        placeholder="None"
        max-height="200px"
        name="name"
        by="value"
        no-border
        block
        @update:modelValue="handleSelectedBy"
      />
    </div>
    <TransitionGroup name="slide-fade" tag="div" class="space-y-4">
      <div v-if="[1, 3].includes(selected.by)">
        <label id="releaseYear" class="font-normal">Select Year</label>
        <Select
          v-model="selected.releaseYear"
          v-loading="loading.releaseYear"
          id="releaseYear"
          :items="state.releaseYear"
          placeholder="None"
          max-height="200px"
          name="release_year"
          by="value"
          no-border
          block
          @update:modelValue="handleYear"
        />
      </div>
      <div v-if="(selected.by === 3 && selected.releaseYear) || selected.by === 2">
        <label id="screenSize" class="font-normal">Select Screen Size</label>
        <Select
          v-model="selected.screenSize"
          v-loading="loading.screenSize"
          id="screenSize"
          :items="state.screenSize"
          placeholder="None"
          max-height="200px"
          name="screen_size"
          by="value"
          no-border
          block
          @update:modelValue="handleScreen"
        />
      </div>
    </TransitionGroup>
  </div>
</template>

<script>
import { ref, defineComponent, watch, computed } from 'vue'
import Select from 'components/Select.vue'
import { useWidgetLandingCombination } from 'composable/setting'

export default defineComponent({
  name: 'WidgetLandingCombination',
  props: {
    modelValue: {
      type: [Array, Object, String, Number, Boolean],
      default: undefined
    },
    selectedProduct: {
      type: Object,
      default: () => ({})
    }
  },
  components: {
    Select
  },
  emits: ['combine'],
  async setup(props, { emit }) {
    const { loading, state, query, show, showToken } = useWidgetLandingCombination()
    const itemsBy = ref([
      { name: 'None', value: null },
      { name: 'By year', value: 1 },
      { name: 'By screen size', value: 2 },
      { name: 'By year & screen size', value: 3 }
    ])
    const selected = ref({
      by: null,
      screenSize: null,
      releaseYear: null,
      tokenModel: null
    })
    const selectedScreen = computed(
      () => (state.value.screenSize && state.value.screenSize.length && [...state.value.screenSize].shift()) || null
    )
    const selectedYear = computed(
      () => (state.value.releaseYear && state.value.releaseYear.length && [...state.value.releaseYear].shift()) || null
    )
    const handleActionShowToken = async () => {
      await showToken({
        ...(selected.value.screenSize && { screen_size: selected.value.screenSize }),
        ...(selected.value.releaseYear && { release_year: selected.value.releaseYear }),
        ...(props.selectedProduct.token_model && { token_model: props.selectedProduct.token_model })
      }, (res) => {
        if (res && res.token_model) {
          selected.value.tokenModel = res.token_model
        }
      })
    }
    const handleActionShow = async (attribute = undefined) => {
      query.value.custom_starting_value = props.selectedProduct.custom_starting_value
      if (attribute) query.value.option_attribute = attribute

      if (!!selected.value.screenSize) query.value.screen_size = selected.value.screenSize
      if (!!selected.value.releaseYear) query.value.release_year = selected.value.releaseYear

      await show()
    }
    const handleSelectedBy = async () => {
      if (selected.value.by) {
        delete query.value.screen_size
        delete query.value.release_year
        selected.value.tokenModel = null
        selected.value.screenSize = null
        selected.value.releaseYear = null

        await handleActionShow(([1, 3].includes(selected.value.by) && 'releaseYear') || 'screenSize')
        const combinator = {
          selected: [1, 3].includes(selected.value.by)
            ? (selectedYear.value && selectedYear.value.value)
            : (selectedScreen.value && selectedScreen.value.value),
          handler: [1, 3].includes(selected.value.by) ? handleYear : handleScreen
        }
        combinator.selected && combinator.handler(combinator.selected)
      }
    }
    const handleScreen = async (slug) => {
      selected.value.tokenModel = null
      selected.value.screenSize = slug

      await handleActionShowToken()
    }
    const handleYear = async (slug) => {
      selected.value.screenSize = null
      selected.value.releaseYear = slug

      if (selected.value.by === 3) {
        await handleActionShow('screenSize')
        return selectedScreen.value && handleScreen(selectedScreen.value.value)
      }
  
      selected.value.tokenModel = null

      await handleActionShowToken()
    }

    watch(() => selected.value.tokenModel, () => {
      emit('combine', selected.value.tokenModel)
    })

    watch(() => props.selectedProduct, (newVal, oldVal) => {
      if (!!oldVal && !!oldVal.search && newVal.search !== oldVal.search) {
        selected.value.by = null
        selected.value.tokenModel = null
        selected.value.screenSize = null
        selected.value.releaseYear = null
        delete query.value.screen_size
        delete query.value.release_year
        state.value = {
          relaseYear: [],
          screenSize: []
        }
      }
    }, {
      immediate: true,
      deep: true
    })

    return {
      state,
      loading,
      itemsBy,
      selected,
      handleYear,
      handleScreen,
      handleSelectedBy
    }
  }
})
</script>