<template>
  <div class="flex flex-col h-full p-6">
    <Breadcrumb
      class="mt-2 mb-8"
      pageTitle="Widget"
      :backTo="{ name: 'setting' }"
      :items="breadcrumb">
    </Breadcrumb>
    <div class="flex-auto">
      <div class="grid grid-cols-8 gap-6 pb-6">
        <div class="xl:col-span-6 col-span-8">
          <WidgetPreview
            v-model="payload"
            :available="stateDevice.is_available_device"
            :loading="loading.all"
            :loading-update="loading.update"
            :white-label="features.whitelabelWidget"
            :tenant-id="widgetOptions.tenantId"
            :api-key="widgetOptions.apiKey"
            :language="selectedCustomStartingPoint.secondary_language"
            :device="selectedCustomStartingPoint.device_slug"
            :brand="selectedCustomStartingPoint.brand_slug"
            :category="selectedCustomStartingPoint.category_slug"
            :token-model="selectedCustomStartingPoint.token_model"
            :token-location="selectedCustomStartingPoint.token_location"
          />
        </div>
        <div class="xl:col-span-2 col-span-8">
          <div
            class="sticky top-8 overflow-y-auto p-4"
            style="max-height: 90dvh"
          >
            <WidgetController
              ref="refWidgetController"
              v-model="payload"
              :disabled-widget="!stateDevice.is_available_device"
              :static-data="staticData"
              :proplan="isProPlan"
              :source="state.source"
              :loading="loading"
              :sort-order="dataSortOrder"
              :custom-starting="stateCustomStartingPoint"
              :remove-brand="isDisabledBuybackBrandingOption"
              :collapsed-section="collapsedSectionByURLQuery"
              :white-label="isWhiteLabel"
              :is-paywalled-widget-watermark="isPaywalledWidgetPlatformWatermark"
              :remove-brand-free="isRemoveBrandFree"
              @update:sortOrder="handleUpdateSort"
              @update:landingPage="handleUpdateLanding"
              @update:removeBrand="handleRemoveBranding"
              @update:language="handleChangeLanguage"
              @update:message="handleSaveMessage"
              @update:customButton="handleSaveCustomButton"
              @update:secondaryLanguage="handleChangeLanguageSecondary"
            />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent, reactive, ref, computed, watch, onMounted } from 'vue'
import { useRoute } from 'vue-router'
import { useWidget } from 'composable/setting'
import { useUser } from 'composable/user'
import { useDevice } from 'composable/dashboard'
import { isWhiteLabel, useFeatureFlag } from 'composable/whitelabel'

import debounce from 'utils/debounce'
import { onDemand } from 'utils/script-loader'
import Card from 'bbUi/components/card/card.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import BaseLoading from 'components/BaseLoading.vue'

import { useVuelidate } from '@vuelidate/core'
import { required, numeric, minValue, helpers } from '@vuelidate/validators'
import { ElMessage } from 'element-plus'
import WidgetController from 'components/Widget/Controller/index.vue'
import WidgetUnvailable from 'components/Widget/Unavailable/index.vue'
import WidgetPreview from 'components/Widget/Preview/index.vue'
import Button from 'bbUi/components/button/button.vue'

export default defineComponent({
  components: {
    Card,
    Button,
    Breadcrumb,
    BaseLoading,
    WidgetController,
    WidgetUnvailable,
    WidgetPreview
  },
  async setup() {
    const breadcrumb = [
      {
        text: 'Settings',
        to: {
          name: 'setting'
        }
      },
      {
        text: 'Widget'
      }
    ]
    const route = useRoute()
    const {
      data: payload,
      modal: modalWidget,
      staticData,
      loading,
      actionShow,
      actionUpdate,
      state,
      stateCustomStartingPoint,
      updateResponse,
      actionRemoveBranding,
      actionCustomStartingPoint,
      actionShowSortOrder,
      dataSortOrder,
      actionShowLanguage,
      updateLanguage
    } = useWidget()
    const { widgetOptions, action: actionShowMe, isProPlan, isPaywalledWidgetPlatformWatermark, isRemoveBrandFree } = useUser()
    const { state: stateDevice, actionConditionDeviceWidgetAvailability } = useDevice()
    const { features } = useFeatureFlag()

    const refWidgetController = ref(null)
    const buyback = ref(null)
    const configUpdateLoading = ref(false)
    const selectedCustomStartingPoint = reactive({
      device_slug: null,
      brand_slug: null,
      category_slug: null,
      token_model: null,
      token_location: null,
      secondary_language: null
    })

    const isDisabledBuybackBrandingOption = computed(() => {
      /* 
        Package ID info:
        Copper = 12
        Silver = 13
        Platinum = 15
        Enterprise = 4
        Pay As you Go = 9
        Starter = 16
        Pro = 17
      */
      // const listOfAllowedPackageId = [4, 13, 15, 17] // [12, 16]
      // return (listOfAllowedPackageId.includes(me.value.package_id)) ? false : true
      return false
    })
    const collapsedSectionByURLQuery = computed(() =>
      route.query.collapse ? route.query.collapse.split(',') : []
    )

    onMounted(async () => {
      if (stateDevice.value.is_available_device) {
        await actionShowSortOrder()
      }
    })
    await actionConditionDeviceWidgetAvailability()
    await actionShow()
    await actionCustomStartingPoint()
    await actionShowLanguage()

    const widgetConfigOption = ref({
      appTypes: [
        {
          name: 'On Page',
          value: 'on_page'
        },
        {
          name: 'Floating',
          value: 'floating'
        }
      ],
      positions: [
        { name: 'Left', value: 'left' },
        { name: 'Top', value: 'top' },
        { name: 'Right', value: 'right' },
        { name: 'Bottom', value: 'bottom' }
      ],
      width_units: [
        { name: 'px', value: 'px' },
        { name: '%', value: '%' }
      ]
    })

    const rules = {
      widget_width: {
        required: helpers.withMessage('Width is required', required),
        numeric,
        minValue: minValue(1)
      }
    }

    const validation = useVuelidate(rules, payload, {
      $lazy: true
    })
    const validate = () => {
      validation.value.$validate()
      return !validation.value.$invalid
    }

    const changeWidget = async () => {
      await actionShow({
        includedKeys: ['source', 'widget_is_remove_brand_available']
      })
    }

    const updateWidget = debounce(async () => {
      const isValid = validate()
      if (isValid) {
        configUpdateLoading.value = true
        await actionUpdate({
          ...payload,
          ...{
            widget_confirm_msg: {
              mailin: payload.widget_confirm_msg.mailin.message,
              instore: payload.widget_confirm_msg.instore.message
            }
          }
        }, () => {
          document.documentElement.style.setProperty(
            '--theme-color',
            `${payload.widget_color_selected}`
          )
          document.documentElement.style.setProperty(
            '--theme-color-hover',
            `${payload.widget_color_selected}cc`
          )
          document.documentElement.style.setProperty(
            '--show-logo',
            `${payload.is_widget_logo_show == '1' ? 'block' : 'none'}`
          )
        }).finally(() => (configUpdateLoading.value = false))
        changeWidget()
      }
    }, 500)

    const handleSaveMessage = async (value) => {
      await actionUpdate({
        ...payload,
        ...value
      })
      await actionShow()
      ElMessage({
        type: 'success',
        message: 'Confirmation message updated!'
      })
    }

    const handleSaveCustomButton = async () => {
      await changeWidget()
      ElMessage({
        type: 'success',
        message: 'Custom button updated!'
      })
    }

    const handleRemoveBranding = async () => {
      await actionRemoveBranding(
        { is_remove_brand: payload.widget_is_remove_brand },
        async () => await changeWidget()
      )
    }
    const handleUpdateSort = async () => await changeWidget()
    const handleChangeLanguage = async (val) => {
      loading.all = true
      await updateLanguage(val, async () => {
        await actionShowMe()
        ElMessage({
          type: 'success',
          message: 'Widget settings updated'
        })
        loading.all = false
      })
    }
    const handleChangeLanguageSecondary = async (val) => {
      selectedCustomStartingPoint.secondary_language = val?.code
      // initWidget()
    }

    watch(
      () => payload,
      debounce(() => {
        payload.widget_width_unit = '%'
        if (payload.widget_width_type === 'full_width') {
          payload.widget_width = '100'
        }
        if (payload.widget_width_type === 'boxed') {
          payload.widget_width = '50'
        }
        if (payload.widget_width_type === 'middle_boxed') {
          payload.widget_width = '25'
        }
        updateWidget()
      }, 500),
      {
        deep: true
      }
    )

    const handleResetLanding = (value) => {
      const keys = Object.keys(selectedCustomStartingPoint)
      keys.forEach(key => {
        selectedCustomStartingPoint[key] = null
      })
      if (value) handleSetNewLanding(Object.keys(value), value)
    }
    const handleSetNewLanding = (keys, value) => {
      keys.forEach(key => {
        if (['token_model_combine', 'token_model'].includes(key)) {
          selectedCustomStartingPoint.token_model = value.token_model_combine || value.token_model
        } else selectedCustomStartingPoint[key] = value[key]
      })
    }
    const handleUpdateLanding = (value) => {
      handleResetLanding(value)
    }
    const handleSaveAll = async () => {
      // Starting Point
      // const startingType = refWidgetController?.value?.widgetStartingPoint?.modelValue?.is_widget_enable_brand_step
      // if (startingType == 3) {
      //   refWidgetController?.value?.widgetStartingPoint?.handleSearchChangeText()
      // }
      // Starting Point

      // Offer Settings
      // Prefix
      await refWidgetController?.value?.widgetOffer.widgetPrefix.handleSave()
      // Price Lock
      await refWidgetController?.value?.widgetOffer.widgetPirceLock.handleSave()
      // Offer Settings
    }

    return {
      payload,
      state,
      features,
      stateDevice,
      staticData,
      stateCustomStartingPoint,
      modalWidget,
      isDisabledBuybackBrandingOption,
      isWhiteLabel,
      collapsedSectionByURLQuery,
      configUpdateLoading,
      changeWidget,
      actionUpdate,
      updateResponse,
      loading,
      breadcrumb,
      widgetConfigOption,
      validation,
      validate,
      handleRemoveBranding,
      handleSaveMessage,
      handleSaveCustomButton,
      dataSortOrder,
      handleUpdateSort,
      handleChangeLanguage,
      handleUpdateLanding,
      selectedCustomStartingPoint,
      isProPlan,
      isPaywalledWidgetPlatformWatermark,
      isRemoveBrandFree,
      widgetOptions,
      handleChangeLanguageSecondary,
      refWidgetController,
      handleSaveAll
    }
  }
})
</script>
